import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { environment } from "../../environments/environment";
@Injectable({
  providedIn: 'root'
})
export class ScheduleService {
  storeId: any;
  // pageName = new BehaviorSubject('Store');
  headers = new HttpHeaders({'Content-Type':'application/json; charset=utf-8'});
  constructor( private http:HttpClient) {

    if( localStorage.getItem('currentUser')){
      var currentUser:any =  localStorage.getItem('currentUser');
      this. storeId=JSON.parse(currentUser);
      var token=this.storeId.token;
      this.headers= new HttpHeaders({ 'Content-Type': 'application/json','Authorization': token});
    }

   }


  bookLiveEvent(data:any){
    ;
    return this.http.put(
      `${environment.BASE_URL}/livevideoservices/bookLiveEvent`,data
    );
  }

  getStoreById(id:any,paramkey:any){
    // ;
    if(paramkey!=null){
    return this.http.get(
      // `${environment.BASE_URL}/store/${id}`
      `${environment.BASE_URL}/store/video/${id}/${paramkey}`
    );
    }
    else{
      return this.http.get(
        // `${environment.BASE_URL}/store/${id}`
        `${environment.BASE_URL}/store/video/${id}/null`
      );
    }
  }

  getLiveSession(id: any) {
    return this.http.get(
      `${environment.BASE_URL}/livevideoservices/${id}`
    );
  }
  getRecordedSession(id: any) {
    return this.http.get(
      `${environment.BASE_URL}/recordedvideoservices/${id}`
    );
  }
  getPackageSession(id: any) {
    return this.http.get(
      `${environment.BASE_URL}/packagevideoservices/${id}`
    );
  }

  getLiveSessionByStoreId(storeId:any){
    return this.http.get(
      `${environment.BASE_URL}/livevideoservices/store/${storeId}`
    );
  }

  getSessionDetailById(type:any,id:any){
    switch(type){
      case 'Live Session': {
        return this.http.get(`${environment.BASE_URL}/livevideoservices/${id}`);
      }
      case 'Recorded Video': {
        return this.http.get(`${environment.BASE_URL}/recordedvideoservices/${id}`);
      }
      case 'Package': {
        return this.http.get(`${environment.BASE_URL}/packagevideoservices/${id}`);
      }
      default: {
        return this.http.get(`${environment.BASE_URL}/livevideoservices/${id}`);
      }
    }
  }


  razorPay(data:any){
    return this.http.post(`${environment.BASE_URL}/user/razorpay/create`,data,{headers:this.headers});
  }

  onPaymentSuccess(data:any){

    return this.http.post(`${environment.BASE_URL}/user/razorpay/success`,data,{headers:this.headers});
  }

  getVideoOrderListByUserId = () => {
    return this.http.get(`${environment.BASE_URL}/order/video-order`,{headers:this.headers});
  }
  
  
  createVideoOrder = (data:any) => {
    ;
    return this.http.post(`${environment.BASE_URL}/order/create/video-order`,data,{headers:this.headers});

  }

  //Favorite Store API

  fetchFavStore = () =>{ 
    return this.http.get(`${environment.BASE_URL}/user/fetchFavouriteStore`,{headers:this.headers});
  }


  addStoreToFav = (storeId:any) => {
    return this.http.post(`${environment.BASE_URL}/user/addFavouriteStore/${storeId}`,{},{headers:this.headers});
  }


  deleteFavStore = (favId:any) => {
    return this.http.delete(`${environment.BASE_URL}/user/deleteFavouriteStore/${favId}`,{headers:this.headers});
  }

  fetchFavProducts = () =>{ 
    return this.http.get(`${environment.BASE_URL}/user/fetchAllFavouriteVideoServices`,{headers:this.headers});
  }


  addProductTofav = (productId:any) => {
    return this.http.post(`${environment.BASE_URL}/user/addFavouriteVideoServices/${productId}`,{},{headers:this.headers});
  }


  deleteFavProduct = (favId:any) => {
    return this.http.delete(`${environment.BASE_URL}/user/deleteAllFavouriteVideoServiceProduct/${favId}`,{headers:this.headers});
  }

  fetchPatrons = (storeId:any) =>{ 
    return this.http.get(`${environment.BASE_URL}/user/fetchStorePatrons/${storeId}`);
  }
  
  fetchCustomers = (storeId:any) =>{ 
    return this.http.get(`${environment.BASE_URL}/user/fetchTotalCustomer/${storeId}`);
  }
  


  // Store Review API Started

  fetchStoreReviewByUserId = () =>{ 
    return this.http.get(`${environment.BASE_URL}/user/fetchStoreReview_user`,{headers:this.headers});
  }
  
  
  addStoreReview = (storeId:any,body:any) => {
    var currentUser:any =  localStorage.getItem('currentUser');
    this. storeId=JSON.parse(currentUser);
    var token=this.storeId.token;
    const headers= new HttpHeaders({'Authorization': token});
    return this.http.post(`${environment.BASE_URL}/user/addStoreReview/${storeId}`,body,{headers:headers});
  }

  
  fetchAllReviewByStoreId = (storeId:any) =>{ 
    return this.http.get(`${environment.BASE_URL}/user/fetchStoreReview/${storeId}`,{headers:this.headers});
  }


  fetchTopReviewByStoreId = (storeId:any, limit:number) =>{ 
    return this.http.get(`${environment.BASE_URL}/user/fetchTopStoreReview/${storeId}?limit=${limit}`);
  }


  fetchTopReviewByProductId = (productId:any, limit:number) =>{ 
    return this.http.get(`${environment.BASE_URL}/user/fetchTopProductReview/${productId}?limit=${limit}`);
  }
  
  
  deleteStoreReview = (storeId:any) => {
    // return this.http.delete(`${environment.BASE_URL}/user/deleteStoreReview/${storeId}`,{headers:this.headers});
  }

// Product Review API Started

getReviewByProduct(type:any, productId:any){
  switch(type){
    case 'Live Session': {
      return this.http.get(`${environment.BASE_URL}/user/fetchVideoServiceReviewByLiveVideoServiceId/${productId}`,{headers:this.headers});
    }
    case 'Recorded Video': {
      return this.http.get(`${environment.BASE_URL}/user/fetchVideoServiceReviewByRecordedVideoServiceId/${productId}`,{headers:this.headers});
    }
    case 'Package': {
      return this.http.get(`${environment.BASE_URL}/user/fetchVideoServiceReviewByPackageVideoServiceId/${productId}`,{headers:this.headers});
    }
    default: {
      return this.http.get(`${environment.BASE_URL}/user/fetchVideoServicesReviewProvidedByUser`,{headers:this.headers});
    }
  }
}

// fetchAllVideoServicesReview(){
//   return this.http.get(`${environment.BASE_URL}/user/fetchAllVideoServicesReview`,{headers:this.headers});
// }

addVideoServiceReview = (videoServiceId:any,body:any) => {
  var currentUser:any =  localStorage.getItem('currentUser');
  this. storeId=JSON.parse(currentUser);
  var token=this.storeId.token;
  const headers= new HttpHeaders({'Authorization': token});
  return this.http.post(`${environment.BASE_URL}/user/addVideoServiceReview/${videoServiceId}`,body,{headers:headers});
}

updateVideoServiceReview = (body:any,videoServiceId:any) => {
  return this.http.put(`${environment.BASE_URL}/user/updateVideoServiceReview/${videoServiceId}`,body,{headers:this.headers});
}

deleteAllVideoServiceProductReview = (reviewVideoServiceId:any,) => {
  return this.http.delete(`${environment.BASE_URL}/user/deleteAllVideoServiceProductReview/${reviewVideoServiceId}`,{headers:this.headers});
}





}

