import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ScheduleService } from '../Services/schedule.service';
import {MatDialog} from '@angular/material/dialog';
import { ScheduleComponent } from '../schedule/schedule.component';
import { LiveStreamComponent } from '../live-stream/live-stream.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { NgxSpinnerService } from "ngx-spinner";
import 'lodash';
declare var _:any;
@Component({
  selector: 'app-store-item-details',
  templateUrl: './store-item-details.component.html',
  styleUrls: ['./store-item-details.component.scss']
})
export class StoreItemDetailsComponent implements OnInit {
  @ViewChild('videoTemplate',{static:true})videoTemplate:any;
  @ViewChild('videoPlayer') videoplayer!: ElementRef;
  @ViewChild('scrollableDiv', { read: ElementRef }) public scrollableDiv!: ElementRef<any>;
  @ViewChild('goToStoreReview') goToStoreReview!: ElementRef<HTMLElement>;
  detailData:any;
  videoId:any;
  imgUrl:any = 'https://valart.s3.us-east-1.wasabisys.com/t_'
  type:any;
  schedule:any;
  scheduleRepeat:any;
  startDate:any;
  endDate:any
  videoSource:any;
  prVideos:any;
  plSchedule:any;
  userId:any;
  // storeId:any;
  sessionId:any;
  bigImageUrl:any;


  localCartData:any;
  cartData:any;
  cartFlag:boolean = false;
  orderFalg:boolean = false;
  orders:any;
  loggedInFlag:boolean = false;
  reviewUrl:any;
  storeId:any;
  selectedReviewData:any[] = [];
  productReviewData:any[] = [];
  storeReviewData:any[] = [];
  selectedReviewType:any = 'STORE'


  constructor(private scheduleService:ScheduleService,public router: Router, private SpinnerService: NgxSpinnerService,
    private actRoute:ActivatedRoute,public dialog: MatDialog, private _snackBar: MatSnackBar, ) {
      this.detailData = {};
      this.detailData.images = []
      this.schedule = []
     }

  ngOnInit(): void {
    window.scrollTo(0,0)
    this.SpinnerService.show();
    // ;
    this.type = this.actRoute.snapshot.paramMap.get('compName');
    this.sessionId = this.actRoute.snapshot.paramMap.get('id');
    this.localCartData = localStorage.getItem('cartData')
    this.localCartData ? this.cartButtonValidation() : this.cartFlag = false;
    this.getDetails(this.type,this.sessionId);

    let currentUser:any =  localStorage.getItem('currentUser');
    let userObj =JSON.parse(currentUser);
    if(userObj){
      this.userId = userObj.user.id;
      this.loggedInFlag = true;
      this.getVideoOrderList();
    }else{
      this.loggedInFlag = false;
    }

    this.storeId =  localStorage.getItem('storeId');
    this.reviewUrl = `https://homebyze.netlify.app/Reviews/Store/${this.storeId}`;
    this.getStoreReviewData();
    this.getProductReviewData();
  }

  getDetails(type:any,id:any){
    ;
    this.scheduleService.getSessionDetailById(type,id).subscribe(
      (response:any)=>{   
        ; 
        this.SpinnerService.hide();    
        this.detailData = response.data;
        // console.log("detailData : ",this.detailData)
        this.videoId = this.detailData._id;
        this.bigImageUrl = this.detailData.images[0];
        if(response.data.scheduleId ){
          this.schedule = response.data.scheduleId.schedule;
          this.scheduleRepeat = response.data.scheduleId.scheduleRepeats;
          this.startDate = response.data.scheduleId.scheduleValidFrom;
          this.endDate = response.data.scheduleId.scheduleValidTill;
        }
        if(response.data.liveVidIds){
          response.data.liveVidIds.length > 0 ? this.plSchedule = response.data.liveVidIds[0].scheduleId.schedule:'';
        }
        if(response.data.recordedVidIds){
          response.data.recordedVidIds.length > 0 ? this.prVideos = response.data.recordedVidIds[0].videofile:'';
        }
      },
      (error:any)=>{
        this.SpinnerService.hide();
        // console.log("Error : ",error);
      }
    )
  }

  getVideoOrderList(){
      this.scheduleService.getVideoOrderListByUserId().subscribe(
        (res:any)=>{
          let response = res.results
          let lArr : Array<any> = [];
          res.results.forEach((element:any) => {
            let ress =  _.map(element.liveVideoIds, 'id');
            lArr.push(ress);
          });
          let rArr = _.map(response, 'recorededVideoIds');
          let pArr = _.map(response, 'packageVideoIds');
          this.orders = _.concat(fn(rArr),fn(lArr),fn(pArr))
          let index = _.findIndex(this.orders, (ele:any) => { return ele == this.sessionId });
          index !== -1 ? this.orderFalg = true : this.orderFalg = false;
        },
        (err:any)=>{
          // console.log("err : ",err);
          this.orderFalg = false;
        }
      )
      const fn = _.spread(_.union);
  }

  cartButtonValidation(){
    ;
      this.cartData = JSON.parse(this.localCartData);
      let index =  _.findIndex( this.cartData, (o:any) => { return o._id == this.sessionId; });
      if(index == -1){
        this.cartFlag = false;
      }else{
        this.cartFlag = true;
      }
  }

  addToCart(){
    if(!this.localCartData){
      ;
      this.cartData = [this.detailData];
    }else{
      ;
      this.cartData.push(this.detailData);
    }
    this.cartFlag = true;
    localStorage.setItem('cartData',JSON.stringify(this.cartData));
  }

  goToCart(){
    this.router.navigate(['/cart'])
  }

  openScheduleDialog(id:any): void {
    const dialogRef = this.dialog.open(ScheduleComponent, {
      // width: '250px',
      data: {id: id}
    });

    dialogRef.afterClosed().subscribe(result => {
      // console.log('The dialog was closed',result);
    });
  }

  joinMeet(selectedSchedule:any,selectedSlot:any){ 

    let flag = this.checkAvailability(selectedSchedule,selectedSlot)
    console.log("Validation FLag : ", flag);

    if(flag){
      // console.log("EV : ",selectedSlot.users);
      let usersData = JSON.stringify(selectedSlot.users);
      localStorage.setItem('usersData',usersData);

      localStorage.setItem('sessionId',this.sessionId);
      this.router.navigate(['/stream',{slotId:selectedSlot._id ,playBackUrl:selectedSlot.playBackUrl}]);
  }else{
    this._snackBar.open('Session Not Available', 'close', {
      duration: 2000,
    });
  } 
  }

  // start button availability code

  checkAvailability(schedule:any,slot:any){
    console.log("TIMEOBJ : ",schedule,slot);
    let sessionTime = slot.time;
    return this.isButtonEnabled(this.startDate,this.endDate, sessionTime,schedule.weekDay);
  }

  isButtonEnabled(startDate:any, endDate:any, sessionTime:any,weekDay:string):boolean{
    
    let sessionStart = this.removeAMOrPM(sessionTime.split("-")[0].trim());
    let sessionEnd = this.removeAMOrPM(sessionTime.split("-")[1].trim());
    let startD = new Date(startDate).getTime();
    let endD = new Date(endDate).getTime();
    let currentD = new Date().getTime();
    let returnableValue = false;
    
    console.log("current is greater than start : ", startD < currentD);
    console.log("current is greater than start : ", endD > currentD);
    if (this.checkIfInRange(startD, endD, currentD, false, false)) {
      let startDateTime = new Date(new Date(startDate).toLocaleDateString() + " " + sessionStart).toLocaleTimeString();
      let endDateTime = new Date(new Date(endDate).toLocaleDateString() + " " + sessionEnd).toLocaleTimeString()
      switch (this.scheduleRepeat) {
        case "Daily":
          if (
            this.checkTime(
              startDateTime,
              endDateTime,
              new Date().toLocaleTimeString()
            )
          ) {
            console.log("PERMIT");
            returnableValue = true;
          } else {
            returnableValue = false;
            console.log("NOT PERMIT");
          }
          break;
        case "Weekly":
          if (weekDay == this.getDayOfWeek()) {
            if (
              this.checkTime(
                startDateTime,
                endDateTime,
                new Date().toLocaleTimeString()
              )
            ) {
              console.log("PERMIT");
              returnableValue = true;
            }
          } else {
            returnableValue = false;
            console.log("NOT PERMIT");
          }
          break;
        case "Monthly":
          if (this.checkIfInRange(weekDay, null, new Date().toLocaleDateString(), false, true)) {
            if (
              this.checkTime(
                startDateTime,
                endDateTime,
                new Date().toLocaleTimeString()
              )
            ) {
              returnableValue = true;
              console.log("PERMIT");
            }
          } else {
            returnableValue = false;
            console.log("NOT PERMIT");
          }
          break;
        case "No thanks":
          if (this.checkIfInRange(weekDay, null, new Date().toLocaleDateString(), true, false)) {
            if (
              this.checkTime(
                startDateTime,
                endDateTime,
                new Date().toLocaleTimeString()
              )
            ) {
              returnableValue = true
              console.log("PERMIT");
            } else {
              returnableValue = false;
              console.log("NOT PERMIT");
            }
          }
          break;
          default:
              console.log("NO CASE FOUND...")
              returnableValue = false;
              break
      }
    }

    return returnableValue;

  }

  removeAMOrPM(time:string) {
    let localTime = ""
    if (time.includes("AM")) {
    localTime = time.replace("AM", "");
  } else {
   time = time.replace("PM", "");
    // let hours = parseInt(time.split(":")[0]) + 12;
    let hours = parseInt(time.split(":")[0]) != 12 ? parseInt(time.split(":")[0]) + 12 : parseInt(time.split(":")[0]);
    localTime = time.replace(time.split(":")[0], hours.toString());
  }
  if(localTime.split(":").length <= 2){
    localTime = localTime.trim() + ":00"
  }
  return localTime
  }

  checkTime(start:any, end:any, current:any) {
    console.log("start : ", start, "end : ", end, "current : ", current);
    let startT = this.removeAMOrPM(start);
    let endT = this.removeAMOrPM(end);
    let currentT = this.removeAMOrPM(current);
    console.log("start : ", startT, "end : ", endT, "current : ", currentT);
    return startT < currentT && endT > currentT;
  }

  checkIfInRange(start:any, end:any, current:any, single = false, day = false) {
    if (day) {
      return start == new Date(current).getDate();
    } else if (single) {
      return start == current;
    } else {
      return start < current && end > current;
    }
  }

 getDayOfWeek() {
    const dayOfWeek = new Date().getDay();
    return isNaN(dayOfWeek)
      ? null
      : [
          "Sunday",
          "Monday",
          "Tuesday",
          "Wednesday",
          "Thursday",
          "Friday",
          "Saturday",
        ][dayOfWeek];
  }


  // End Button availability Code
 
  watchVideo(src:any){
    this.router.navigate(['/video',{src:src}])
  }

  toggleVideo(){

  }

  // checkPurchased = (schObj:any) => {
  //     console.log("schObj : ",schObj , "userId : ", this.userId);
      
  // }

  checkAvailableBooking(value:any){
    if(value.users && value.users.length > 0){
      let index = _.findIndex(value.users, (ele:any) => { return ele._id == this.userId});
      if(index !== -1)  return true;
      else return false;
    }else{
      return false;
    }
  }


  checklableOrder = (schObj:any) => {
    // console.log("schObj : ", schObj);
    let res = false;
    schObj.forEach((element:any) => {
      let ans =  this.checkAvailableBooking(element);
      if(ans){
        res = ans;
      }
    });
    return res;
  }

  loadMethod = (event:any) => {
    this.getDetails(this.type,this.sessionId);
    this.getVideoOrderList();
  }

  showSelectedImage = (url:any) => {
    this.bigImageUrl = url;
  } 

  scrollUp(el: any) {
        this.scrollableDiv.nativeElement.scrollTo({ top: (this.scrollableDiv.nativeElement.scrollTop  - 95), behavior: 'smooth' });
  }

  scrollDown(el: any) {
        this.scrollableDiv.nativeElement.scrollTo({ top: (this.scrollableDiv.nativeElement.scrollTop  + 95), behavior: 'smooth' });
  }

  goToReview = () => {
    // let el: HTMLElement = this.goToStoreReview.nativeElement;
    // el.click();

    this.router.navigate(['/store-review',{type:this.selectedReviewType,store:this.storeId, product:this.sessionId}]);

  }

  toggleReview = (ev:string) => {
    if(ev === 'HOMESTROE'){
      this.selectedReviewType = 'STORE'
      this.selectedReviewData = this.storeReviewData
    }else{
      this.selectedReviewType = 'PRODUCT'
      this.selectedReviewData = this.productReviewData
    }
  }

  getStoreReviewData=()=>{
    this.scheduleService.fetchTopReviewByStoreId(this.storeId,5).subscribe((res:any)=>{
      // console.log("RES : ",res)
      this.storeReviewData = res.message;
      this.selectedReviewData = this.storeReviewData
    },
    (err:any)=>{
      // console.log("ERR : ",err)
    })
  };

  getProductReviewData=()=>{
    this.scheduleService.getReviewByProduct(this.type, this.sessionId).subscribe((res:any)=>{
      // console.log("RES : ",res)
      this.productReviewData = res.message;
    },
    (err:any)=>{
      // console.log("ERR : ",err)
    })
  };

}
