import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { VideoStreamService } from '../Services/video-stream.service';
import { environment } from "../../environments/environment";
import 'lodash';
import { ScheduleService } from '../Services/schedule.service';
import { NgxSpinnerService } from 'ngx-spinner';
declare var _: any;
declare var Razorpay: any;
@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.scss']
})
export class CartComponent implements OnInit {
  
  @ViewChild('goHomeUrl') goHomeUrl!: ElementRef<HTMLElement>;
  @ViewChild("tshopping",{static:false}) tshopping: any;
  imgUrl: any = 'https://valart.s3.us-east-1.wasabisys.com/';
  userId: any;
  storeId: any;
  localCartData:any;
  cartData:any[] = [];
  cartFalg:boolean = false;
  basicTotal:number = 0;
  totalPrice:any;
  gstPrice:any;
  liveVideoIds:any[]= [];
  packageVideoIds:any[]= [];
  recorededVideoIds:any[]= [];
  totalProducts:number= 0;
  recordedPrice:number = 0;
  packagePrice:number = 0;

  constructor(
    private videoStreamService: VideoStreamService,
    private scheduleService: ScheduleService,
    private router: Router,
    private dialog: MatDialog,
    private SpinnerService: NgxSpinnerService,
  ) { 
    ;
    // this.scheduleService.pageName.next('Cart');
    console.log("cart length : ",this.cartData.length)
    if(localStorage.getItem('currentUser')){
    var storedData: any = localStorage.getItem('currentUser');
    storedData = JSON.parse(storedData);
    if(localStorage.getItem('storeId')){
      this.storeId = localStorage.getItem('storeId')
    }else{
      this.storeId = storedData.user.storeId;
    }
    this.userId = storedData.user.id;
    }
  }

  ngOnInit(): void {
    ;
    this.loadRazorpayScript("https://checkout.razorpay.com/v1/checkout.js").then(console.log);
    this.localCartData = localStorage.getItem('cartData');
    if (this.localCartData) {
      this.cartData = JSON.parse(this.localCartData);
      this.totalProducts = this.cartData.length
      console.log("cart length : ",this.cartData)
      this.calculateTotalAndGST();
    } else {
      this.cartFalg = true;
    }
    // this.getCartData();
  }

  calculateTotalAndGST = () => {

    this.cartData.forEach((ele:any)=>{
      if(ele.type=="recordedVideo"){
        this.recordedPrice += Number(ele.price);
      }else{
        this.packagePrice += Number(ele.price);
      }
    });
    ;
    this.basicTotal = _.sumBy(this.cartData, (obj:any) => { return parseInt(obj.price) }); 
    var gstRate=this.cartData[0].marginId.rate
    this.gstPrice = (this.basicTotal * gstRate ) / 100;
    this.gstPrice = parseInt(this.gstPrice);
    this.totalPrice = this.basicTotal + this.gstPrice 
  }

  loadRazorpayScript = (src: any) => {
    return new Promise((resolve) => {
      const script = document.createElement("script");
      script.src = src;
      script.onload = () => {
        resolve(true);
      };
      script.onerror = () => {
        resolve(false);
      };
      document.body.appendChild(script);
    });
  };

  // getCartData(){
  //   this.videoStreamService
  //   .getVideoByStoreId('liveVideo')
  //   .subscribe((response: any) => {
  //     this.cartData = response.data;
  //   });
  // }

  proceedToBuy = ():any => {
    ;
    if(localStorage.getItem('loginToken')){
      this.proceed();
    }else{
      let el: HTMLElement = this.goHomeUrl.nativeElement;
      el.click();
    }
  }

  proceed = () => {
    ;
    let ammount = parseInt(this.totalPrice)*100;
     this.videoStreamService.razorPay({ amount: ammount }).subscribe((razorpay:any)=>{
       let options = {
        "key": "rzp_test_WRhbV3UAwa25Na", // Enter the Key ID generated from the Dashboard
        "amount": razorpay.message.amount, // Amount is in currency subunits. Default currency is INR. Hence, 50000 refers to 50000 paise
        "currency": "INR",
        "name": "HOMEBYZ",
        "description": "Test Transaction",
        "image": "https://res.cloudinary.com/ravipatil8286/image/upload/v1616133398/logo01_2x_mgevpr.png",
        "order_id": razorpay.message.id, //This is a sample Order ID. Pass the `id` obtained in the response of Step 1
        "handler": (response: any) => {
          console.log("raser response : ", response);

          const data165 = {
            razorpay_payment_id: response.razorpay_payment_id,
            razorpay_order_id: response.razorpay_order_id,
            razorpay_signature: response.razorpay_signature
          }
          // alert('thanks for shopping with us')
          this.dialog.closeAll();
          this.dialog.open(this.tshopping);
          this.videoStreamService.onPaymentSuccess(data165).subscribe(
            (response: any) => {
              this.saveOrder(razorpay.savedData._id);
          // alert('thanks for shopping with us')
            },
            (error: any) => {

            }
          )
        },
        "notes": {
          "address": "Razorpay Corporate Office"
        },
        "theme": {
          //"color": "#F37254"
          "color": "#F70606"
        }
      };
      const paymentObject = new Razorpay(options);
      paymentObject.open();
    }, (err: any) => {
      console.log("ERR : ", err);
    })
  }

  saveOrder(razerId: any) {

    console.log("cart Data  : ", this.cartData);

    this.cartData.forEach((element: any) => {

      if (element.type == 'recordedVideo') {
        this.recorededVideoIds.push(element._id)
      } else {
        this.packageVideoIds.push(element._id)
      }
    });


    let data = {
      "userId": this.userId,
      "storeId": this.storeId,
      "liveVideoIds": this.liveVideoIds,
      "packageVideoIds": this.packageVideoIds,
      "recorededVideoIds": this.recorededVideoIds,
      "total": this.totalPrice,
      "razorpayId": razerId
    }

    console.log("Order data", data);
    this.SpinnerService.show()

    this.scheduleService.createVideoOrder(data).subscribe(
      (res) => {
        this.SpinnerService.hide()
        console.log("res : ", res);
        localStorage.removeItem('cartData');
        this.router.navigate(['/store',this.storeId]);
        // alert('thanks for shopping with us')
      },
      (err: any) => {
        this.SpinnerService.hide()
        console.log("err : ", err);

      }
    )
  }

  onRemoveItem = (cartObject: any) => {
    this.cartData = _.remove(this.cartData, function (Obj: any) {
      return JSON.stringify(Obj) != JSON.stringify(cartObject);
    });
    localStorage.setItem('cartData', JSON.stringify(this.cartData));
    this.calculateTotalAndGST();
  }

 

}
