import { Component, OnInit } from '@angular/core';
import { ScheduleService } from '../Services/schedule.service';
import { ActivatedRoute, Router } from '@angular/router';
import { ScheduleComponent } from '../schedule/schedule.component';


@Component({
  selector: 'app-live-video-order',
  templateUrl: './live-video-order.component.html',
  styleUrls: ['./live-video-order.component.scss']
})
export class LiveVideoOrderComponent implements OnInit {
  scheduleData:any;
  videoId:any;
  basicTotal: any;
  gstPrice: any;
  totalPrice: any;

  constructor(private scheduleService:ScheduleService, private actRoute:ActivatedRoute,private ScheduleObj:ScheduleComponent) { }

 async ngOnInit(){
    ;
    this.videoId = this.actRoute.snapshot.paramMap.get('id');

  await this.getSessionById();
    this.calculateTotalAndGST();
  }

 async getSessionById() {
    ;
    var response:any=await this.scheduleService.getLiveSession(this.videoId).toPromise();
    

        this.scheduleData = response.data;

        console.log("scheduleData : ",this.scheduleData);
        localStorage.setItem("liveSession",JSON.stringify(this.scheduleData));
    
    

  }
  proceedToBuy(){
    this.ScheduleObj.proceedToBuy();
    
  }


calculateTotalAndGST = () => {

  // this.cartData.forEach((ele:any)=>{
  //   if(ele.type=="recordedVideo"){
  //     this.recordedPrice += Number(ele.price);
  //   }else{
  //     this.packagePrice += Number(ele.price);
  //   }
  // });
    ;
   this.basicTotal =  parseInt(this.scheduleData.price)  
   var gstRate=this.scheduleData.marginId.rate;
   this.gstPrice = (this.basicTotal * gstRate ) / 100;
   this.gstPrice = parseInt(this.gstPrice);
   this.totalPrice = this.basicTotal + this.gstPrice 
}

onRemoveItem(data:any){

}
}