import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ScheduleService } from '../Services/schedule.service';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-store-review',
  templateUrl: './store-review.component.html',
  styleUrls: ['./store-review.component.scss']

})
export class StoreReviewComponent implements OnInit {
  @ViewChild("reviewmsg", { static: false }) reviewmsg: any;
  storeId: any;
  productId: any;
  selectedReviewType: any = 'HOME';
  dialogmsg: any;

  reviewForm: FormGroup;
  imgURL: any = [];
  imgs: any = [];
  alertMsg: any;
  constructor(
    private scheduleService: ScheduleService,
    private router: Router,
    private actRoute: ActivatedRoute,
    private dialog: MatDialog) {

    this.reviewForm = new FormGroup({
      storeId: new FormControl(''),
      subject: new FormControl(''),
      description: new FormControl(''),
      rating: new FormControl(''),
      isApproved: new FormControl(''),
      images: new FormControl(''),
    });

  }

  ngOnInit(): void {

    this.storeId = this.actRoute.snapshot.paramMap.get('store');
    this.productId = this.actRoute.snapshot.paramMap.get('product');
    this.selectedReviewType = this.actRoute.snapshot.paramMap.get('type');
    this.reviewForm.patchValue({
      storeId: this.storeId,
      rating: 3.5,
      isApproved: true
    });
    console.log("StoreId : ", this.storeId, "ProductId : ", this.productId);
  }

  toggleReview = (ev: string) => {
    if (ev === 'HOMESTROE') {
      this.selectedReviewType = 'STORE'
      // this.selectedReviewData = this.storeReviewData
    } else {
      this.selectedReviewType = 'PRODUCT'
      // this.selectedReviewData = this.productReviewData
    }
    this.reviewForm.reset();
    this.reviewForm.patchValue({
      storeId: this.storeId,
      rating: 3.5,
      isApproved: true
    });
  }

  saveReview = () => {

    if (this.reviewForm.valid) {
      console.log("FORM VALUES :", this.reviewForm.value);
      // this.SpinnerService.show();
      // console.log("Vzideos : ", this.recordedVideoForm.value.videofile );

      let formData: any = new FormData();
      formData.append('storeId', this.reviewForm.value.storeId);
      formData.append('subject', this.reviewForm.value.subject);
      formData.append('description', this.reviewForm.value.description);
      formData.append('rating', this.reviewForm.value.rating);
      formData.append('isApproved', this.reviewForm.value.isApproved);
      if (this.selectedReviewType == 'STORE') {
        this.imgs.forEach((element: any) => {
          formData.append('images', element);
        });
      } else {
        this.imgs.forEach((element: any) => {
          formData.append('videoServiceImages', element);
        });
      }

      this.save(formData);
    } else {
      this.alertMsg = 'Enter Valid Data';
      // this.dialog.open(this.invaliddata);
    }
  }

  save = (payload: any) => {

    console.log("payload : ", payload);

    if (this.selectedReviewType == 'STORE') {
      this.scheduleService.addStoreReview(this.storeId, payload).subscribe((res: any) => {
        console.log("Res : ", res);
        // alert("store review Added Successfully");
        this.dialogmsg = "store review Added ";
        this.dialog.closeAll();
        this.dialog.open(this.reviewmsg);
      }, (err: any) => {
        console.log("ERR : ", err);
      });
    } else {
      this.scheduleService.addVideoServiceReview(this.productId, payload).subscribe((res: any) => {
        console.log("Res : ", res);
        // alert("product review Added Successfully");
        this.dialogmsg = "store review Added ";
        this.dialog.closeAll();
        this.dialog.open(this.reviewmsg);
      }, (err: any) => {
        console.log("ERR : ", err);
      });
    }

  }

  handleError() {
    this.alertMsg = null;
  }
  showPic(event: any) {
    let file = (event.target).files[0];
    console.log("file:", file);
    this.reviewForm.controls.images.setValue(file, { emitModelToViewChange: false });
    const reader = new FileReader();
    reader.onload = () => {
      let url = reader.result as string;
      this.imgURL.push(url);
    }
    this.imgs.push(file);

    reader.readAsDataURL(file);

  }

}
