  generateTime(interval: string) {
    this.times = [];
    this.selectedLiveSchedule = [
      { weekDay: '', schtime: [{ time: '', status: '' }] },
    ];
    this.selectedItem = {};
    let x;

    if (interval == '15 Min') x = 15;
    else if (interval == '30 Min') x = 30;
    else x = 60;

    let startTime = 0;
    let ap = ['AM', 'PM'];

    for (let i = 0; startTime < 24 * 60; i++) {
      let hh = Math.floor(startTime / 60);
      let mm = startTime % 60;
      this.times[i] =
        ('0' + (hh % 12)).slice(-2) +
        ':' +
        ('0' + mm).slice(-2) +
        ap[Math.floor(hh / 12)];
      startTime = startTime + x;
    }
    if (this.liveSessionForm && this.liveSessionForm.value.scheduleRepeats) {
      this.scheduleRepeats = this.liveSessionForm.value.scheduleRepeats;
    } else if (this.packageSessionForm && this.packageSessionForm.value.scheduleRepeats) {
      this.scheduleRepeats = this.packageSessionForm.value.scheduleRepeats;
    } else {
      this.scheduleRepeats = "Daily";
    }
    this.generateCalender(this.scheduleRepeats);
  }

  generateCalender(scheduleRepeats: any) {

    this.scheduleRepeats = scheduleRepeats;
    this.days = [];
    this.liveCalenderData = [];

    this.selectedItem = {};

    switch (scheduleRepeats) {
      case 'Daily': {
        this.days = ['Daily'];
        this.selectedLiveSchedule = [
          { weekDay: '', schtime: [{ time: '', status: '' }] },
        ];
        break;
      }
      case 'Weekly': {
        this.days = [
          'Monday',
          'Tuesday',
          'Wednesday',
          'Thursday',
          'Friday',
          'Saturday',
          'Sunday',
        ];
        this.selectedLiveSchedule = [
          { weekDay: '', schtime: [{ time: '', status: '' }] },
        ];
        break;
      }
      case 'Monthly': {
        for (let i = 1; i <= 31; i++) {
          this.days.push(JSON.stringify(i));
        }
        this.selectedLiveSchedule = [
          { weekDay: '', schtime: [{ time: '', status: '' }] },
        ];
        break;
      }
      case 'No thanks': {
        this.days = [this.selectedCustomeDate];
        break;
      }
      default: {
        for (let i = 1; i <= 31; i++) {
          this.days.push(JSON.stringify(i));
        }
        this.selectedLiveSchedule = [
          { weekDay: '', schtime: [{ time: '', status: '' }] },
        ];
      }
    }

    for (let i = 0; i < this.days.length; i++) {
      this.liveCalenderData[this.days[i]] = [];

      for (let j = 0; j < this.times.length; j++) {
        if (this.times[j + 1]) {
          this.liveCalenderData[this.days[i]].push({
            time: this.times[j] + ' - ' + this.times[j + 1],
            status: '',
          });
        } else {
          this.liveCalenderData[this.days[i]].push({
            time: this.times[j],
            status: '',
          });
        }
      }
      if (this.scheduleRepeats == 'No thanks') {
        let liveRes = this.selectedLiveSchedule.findIndex(
          (element) => element.weekDay == this.days[i]
        );
        if (liveRes != -1) {
          let schtimeArr = [];
          schtimeArr = this.selectedLiveSchedule[liveRes].schtime;
          let liveResArr = _.indexBy(schtimeArr, 'time');

          this.liveCalenderData[this.days[0]].forEach((element: any) => {
            if (liveResArr[element.time]) {
              element.status = liveResArr[element.time].status;
            }
          });
        }

      }
    }

    for (let i = 0; i < this.days.length; i++) {
      this.packageCalenderData[this.days[i]] = [];

      for (let j = 0; j < this.times.length; j++) {
        if (this.times[j + 1]) {
          this.packageCalenderData[this.days[i]].push({
            time: this.times[j] + ' - ' + this.times[j + 1],
            status: '',
          });
        } else {
          this.packageCalenderData[this.days[i]].push({
            time: this.times[j],
            status: '',
          });
        }
      }

      if (this.scheduleRepeats == 'No thanks') {
        let packRes = this.selectedPackageSchedule.findIndex(
          (element) => element.weekDay == this.days[i]
        );
        if (packRes != -1) {
          let schtimeArr = [];
          schtimeArr = this.selectedPackageSchedule[packRes].schtime;
          let packResArr = _.indexBy(schtimeArr, 'time');
          this.packageCalenderData[this.days[0]].forEach((element: any) => {
            if (packResArr[element.time]) {
              element.status = packResArr[element.time].status;
            }
          });
        }
      }
    }
  }

