  updateValstoreDoc() {

    this.SpinnerService.show();
    this.formData.append("fssai", this.fssiImg);
    for (var i = 0; i < this.adharPik.length; i++) {
      this.formData.append("aadharCardImages", this.adharPik[i]);
    }
    for (var i = 0; i < this.panImg.length; i++) {
      this.formData.append("panCardImages", this.panImg[i]);
    }
    this.formData.append("cancelledChequeImage", this.chekPik);

    this.formData.append("landmark", this.ValstoreDocForm.value.landMark);
    this.formData.append("tocStatus", this.ValstoreDocForm.value.registeredAs);
    this.formData.append("gst", this.ValstoreDocForm.value.gst);
    if (this.ValstoreDocForm.value.gst === false) {
      this.formData.append("gstNumber", "");
    }
    if (this.ValstoreDocForm.value.gst === true) {
      this.formData.append("gstNumber", this.ValstoreDocForm.value.GSTnum);
    }
    this.formData.append("aadhaar", this.ValstoreDocForm.value.aadharNo);
    this.formData.append("panNumber", this.ValstoreDocForm.value.panNo);
    this.formData.append("postalAddress", this.ValstoreDocForm.value.postalAddr);
    this.formData.append("pincode", this.ValstoreDocForm.value.zipcode);
    this.formData.append("city", this.ValstoreDocForm.value.city);
    this.formData.append("state", this.ValstoreDocForm.value.state);
    this.formData.append("phoneNumber", this.ValstoreDocForm.value.zipcode);
    // formData.append("storeImagesId"," ");
    this.formData.append("accountType", this.ValstoreDocForm.value.accType);
    this.formData.append("accountNumber", this.ValstoreDocForm.value.accNum);
    this.formData.append("ifscCode", this.ValstoreDocForm.value.ifscCode);
    this.formData.append("branch", this.ValstoreDocForm.value.branch);
    this.formData.append("accountHolderName", this.ValstoreDocForm.value.accHolderName);
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    var token = store.token;
    const storeId = store.user.storeId;
    const headers = new HttpHeaders({ 'Authorization': token });
    return this.httpClient.put(`${environment.BASE_URL}/store/update/${storeId}`, this.formData, { headers: headers }).subscribe((res: any) => {
      console.log(res);
      this.SpinnerService.hide();
      this.router.navigateByUrl("/setupvalstore");
      this.ValstoreDocForm.reset();
    }, (error) => {
      this.SpinnerService.hide();
      alert("something wrong")
      console.log(error);

    })
  }
