  validateAadhar1(obj: any) {
    console.log(obj);
    var regexp = new RegExp('^[2-9]{1}[0-9]{3}\\s[0-9]{4}\\s[0-9]{4}$');
    var test = regexp.test(obj);
    if (!test) {
      this.aadharErr = true;
      console.log("fail");

      return;
    }
    this.aadharErr = false



  }

  validateAadhar(obj: any) {

    var regexp = new RegExp('^[2-9]{1}[0-9]{3}\\s[0-9]{4}\\s[0-9]{4}$');
    var test = regexp.test(obj);
    if (!test) {
      // alert("aadhar number should contain only 12 numbers in 0000 0000 0000 format"); 
      this.aadharErr = true;
      return;
    }
    //  const regex_pattern="";


    var aadharNumber = obj;
    if (aadharNumber.length != 14) {
      //  alert("Aadhar should be 12 Digit");
      obj.value = "";
      obj.focus();
      this.aadharErr = true;
      return;
    }

    var c = 0;
    var invertedArray = aadharNumber.split('').map(Number).reverse();
    var len = invertedArray.length;
    for (var i = 0; i < len; i++) {
      c = this.d[c][this.p[(i % 8)][invertedArray[i]]];
    }
    this.aadharErr = false;
    return (c === 0);
  }
