  validateGST(gst: any) {

    var regexp = new RegExp("^([0][1-9]|[1-2][0-9]|[3][0-7])([a-zA-Z]{5}[0-9]{4}[a-zA-Z]{1}[1-9a-zA-Z]{1}[zZ]{1}[0-9a-zA-Z]{1})+$");
    var test = regexp.test(gst);
    if (!test) {
      //  alert("GST number should be in correct format"); 
      this.gstErr = true;
      return;
    }
    this.gstErr = false;

    // for server validation
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    var token = store.token;
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': token });
    const data = {
      gstNumber: gst
    }
    this.httpClient.post(environment.BASE_URL + "/store/check/unique-data/gst", data, { headers: headers }).subscribe((res: any) => {
      this.gstValidation = res.message;
      console.log(res);

    })
  }


  validatePan(pan: any) {

    var regexp = new RegExp("[A-Z]{5}[0-9]{4}[A-Z]{1}");
    var test = regexp.test(pan);
    if (!test) {
      //  alert("PAN card number should be in correct format"); 

      this.panErr = true;
      return;
    }
    this.panErr = false;

    // for server validation
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    var token = store.token;
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': token });
    const data = {
      panNumber: pan
    }
    this.httpClient.post(environment.BASE_URL + "/store/check/unique-data/pan", data, { headers: headers }).subscribe((res: any) => {
      this.panValidation = res.message
      console.log(res);

    })

  }


  onKeyUp(element: any) {

    var ele: any;
    ele = (<HTMLInputElement>document.getElementById(element)).value;
    ele = ele.split(' ').join('');
    var finalVal = ele.match(/.{1,4}/g).join(' ');
    (<HTMLInputElement>document.getElementById(element)).value = finalVal;
  }

  validateIFSC(ifsc: any) {
    var regexp = new RegExp("^[A-Z]{4}[0][A-Z0-9]{6}$");
    var test = regexp.test(ifsc);
    if (!test) {
      //  alert("PAN card number should be in correct format"); 
      this.ifscErr = true;

      return;
    }
    this.ifscErr = false;

  }
