import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable } from 'rxjs';
import { AdminService } from './admin.service';

@Injectable({
  providedIn: 'root'
})
export class ActivatedGardService implements CanActivate {

  constructor(private router: Router, private adminService: AdminService) { }
  canActivate(): boolean {
    // const status: any = localStorage.getItem("storeStatus")
    const local: any = localStorage.getItem('currentUser')
    const res = JSON.parse(local);
    if (res != null) {
      //  alert("you have permition to access this content");

      return true;
    }
    else {
      // alert("you dont have permition to access this content.");
      this.router.navigateByUrl('/home')
      return false;

    }
  }

}
