import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from "../../environments/environment";
@Injectable({
  providedIn: 'root'
})
export class RegisterService {

  headers = new HttpHeaders({ 'Content-Type': 'application/json; charset=utf-8' });
  constructor(private http: HttpClient) { }
  resendOtpForMobile( mobile: any, token: any): Observable<any> {
    // ;
    const body={
      mobile:mobile,
      token:token
    }
    const headers = new HttpHeaders({ 'Authorization': token });
    return this.http.put(environment.BASE_URL + '/resend-otp/mobile/forget-password', body, { headers: headers });
  }
  resendOtpForEmail( email: any, token: any): Observable<any> {
    // ;
    const body={
     email:email,
      token:token
    }
    const headers = new HttpHeaders({ 'Authorization': token });
    return this.http.put(environment.BASE_URL + '/resend-otp/email/forget-password', body, { headers: headers });
  }



  forgetPswordForMobile(d1: { mobile: any; }): Observable<any> {

    return this.http.put(environment.BASE_URL + "/register/forgot-password/mobile", d1)
  }
  
  
  existingUserMakeStoreAdmin(body: { email: any; password: any; }): Observable<any> {
    console.log("Inside Login service");

   return this.http.post(`${environment.BASE_URL}/register/store-admin/add/role`, body);
  }


  Register(fullname: any, mobno: any, email: any, password: any, city: any, state: any, pincode: any, gender: any, describesyou: any) {
   
    const body = {
      username: "",
      fullName: fullname,
      email: email,
      password: password,
      mobile: mobno,
      pincode: pincode,
      address: "sai nagar",
      city: city,
      state: state,
      gender: gender,
      occupation: describesyou,
      //  role: "storeAdmin"
      role: ["storeAdmin", "user"]
    };

   
    return this.http.post(`${environment.BASE_URL}/register/signup`, body);
  }


  Login(email: any, password: any) {
    console.log("Inside Login service");
    const body = {
      email: email,
      password: password
    }

    return this.http.post(`${environment.BASE_URL}/register/store/login`, body);
  }

  accountActivation(token: any, otp: any, otpMobile: any) {
    // const headers=new HttpHeaders({'Access-Control-Allow-Origin':'*'})
    const body = {
      otp: otp,
      otpMobile: otpMobile
    }
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': token });
    return this.http.post(`${environment.BASE_URL}/register/activation-account`, body, { headers: headers });

  }

  getStoreData(id: any) {
    return this.http.get(`${environment.BASE_URL}/store/${id}`);
  }

  forgotPassword(email: any) {
    const body = {
      "email": email
    }
    return this.http.put(`${environment.BASE_URL}/register/forgot-password`, body);
  }

  submitOtp(otp: any, token: any) {
    const body = {
      "otp": otp
    }

    const headers = new HttpHeaders({ 'Authorization': token });
    return this.http.put(`${environment.BASE_URL}/register/verify-otp`, body, { headers: headers });
  }

  resetPwd(pwd: any, token: any) {
    const body = {
      "newpassword": pwd

    }
    const headers = new HttpHeaders({ 'Authorization': token });
    return this.http.put(`${environment.BASE_URL}/register/reset-password`, body, { headers: headers });
  }

}

