import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { AdminService } from './admin.service';

@Injectable({
  providedIn: 'root'
})
export class VideosGardService implements CanActivate {
  storeType: any;


  constructor(private router: Router, private adminService: AdminService) { }
  canActivate(): boolean {
    const local: any = localStorage.getItem('currentUser')
    const res = JSON.parse(local);
    const token = res.token;
    const role = res.user.role[0]
    this.storeType = localStorage.getItem('storeType');
    if (this.storeType === "Services" || role ==="superAdmin") {
      return true;
    }
    else {
      // alert("you dont have permition to access this content.");
      this.router.navigate(['admin/dashbord'])
      return false;

    }
  }
}
