import { Component, ViewChild } from '@angular/core';
import {
  FormControl, FormGroup, Validators
} from '@angular/forms';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { DataService } from '../Services/data.service';
import { RegisterService } from '../Services/register.service';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent {

  @ViewChild('exampleModalCenter', { static: true }) closebutton: any;
  mainheader: boolean = false;
  resetpwd: boolean = false;
  loginErrMsg: any;
  loginErr: boolean = false;
  otp: boolean = false;
  showPassword: boolean = false;
  alertMsg: any;
  otpresent:boolean=false;

  forgotPwdForm = new FormGroup({
    email: new FormControl(),
    mobile: new FormControl(),
    mobileOrEmail: new FormControl()
  });
  forgotPwdForm2 = new FormGroup({
    otp: new FormControl()
  });
  Loginform = new FormGroup({
    email: new FormControl(),
    password: new FormControl(),
  });

  forgotPwdForm3 = new FormGroup({
    password: new FormControl()
  })
  otpToken: any;
  loginMode = true;
  forgetPasswordForm1 = false;
  mobileNo: any;
  emailId: any;
  OtpMsg = "";
  emailStatus = false;
  @ViewChild('exampleModalCenter') exampleModalCenter: any;
  constructor(private data: DataService, private SpinnerService: NgxSpinnerService, private RegisterServiceobj: RegisterService, private router: Router) {

    this.otp=false;
    this.resetpwd=false;
  }

  chnageEmailStatus() {
    if (this.Loginform.controls.email.valid) {
      this.emailStatus = true;
    }
    if (this.Loginform.controls.email.invalid) {
      this.emailStatus = false;
    }
    console.log(this.emailStatus);

  }
  ngOnInit(): void {
    this.Loginform = new FormGroup({
      email: new FormControl(''),
      password: new FormControl(''),
    });
    //  Validators.pattern("^((\\+91-?)|0)?[0-9]{10}$")]
    this.Loginform.reset();
    this.otp=false;
    this.resetpwd=false;
  }


  gotoregister() {
    this.data.setMainheader(this.mainheader);
  }

  openModal() {
    this.exampleModalCenter.show()

  }
  toggleShowPassword() {
    this.showPassword = !this.showPassword;
  }
  swithchMode() {
    this.loginMode = !this.loginMode;
  }
  existingUser() {
    this.SpinnerService.show();
    const body = {
      email: this.Loginform.value.email,
      password: this.Loginform.value.password
    }
    this.RegisterServiceobj.existingUserMakeStoreAdmin(body).subscribe((res: any) => {
      console.log(res);
      this.SpinnerService.hide();
      //     this.alertMsg = res.message;
      this.router.navigate(['/regvalstore']);


    }, (error) => {
      this.SpinnerService.hide();
      console.log(error);

    })
  }
  Login() {
    this.loginErr = false;
    console.log(this.Loginform);
    console.log("Inside Login Func");
    this.SpinnerService.show();
    this.RegisterServiceobj.Login(this.Loginform.value.email, this.Loginform.value.password).subscribe(
      (response: any) => {
        this.SpinnerService.hide();
        console.log(response);
        const user = response.user;
        const role = user.role;

        this.Loginform.reset();
        this.closebutton.nativeElement.click();
        localStorage.setItem('currentUser', JSON.stringify(response));
        if (response.user.storeId != "") {

          this.RegisterServiceobj.getStoreData(response.user.storeId).subscribe(
            (res: any) => {

              console.log("store resp: ", res);
              const data = {
                storeName: res[0].name,
                logo: res[0].storeImagesId.storeLogo
              }
              localStorage.setItem("storeDetails", JSON.stringify(data))
              const storeStatus = res[0].verified;
              localStorage.setItem("storeStatus", storeStatus);
              console.log(role);
              if (res[0].storeType) {
                //  // 
                localStorage.setItem('storeType', res[0].storeType);

                if (role.includes("storeAdmin")) {
                  this.router.navigate(['/admin']);
                }
                if (role.includes("superAdmin")) {
                  this.router.navigate(['/superAdmin']);
                }
              }

            },
            (err: any) => {

              console.log("store err: ", err);
            }
          )
        }
        if (role.includes("superAdmin")) {
          this.router.navigateByUrl("/superAdmin");
        }
        console.log(role);

        console.log(response.user.storeId, role.includes("storeAdmin"));

        if (response.user.storeId === "" && role.includes("storeAdmin")) {
          console.log("at null storeid");
          this.router.navigate(['/regvalstore']);
          this.SpinnerService.hide();
        }
        // if (response.user.storeId === "" && role === "storeAdmin") {
        //   console.log("at null storeid");
        //   this.router.navigate(['/regvalstore']);
        //   this.SpinnerService.hide();
        // }
      },
      (error: any) => {

        console.log("store err:", error);
        this.loginErr = true;
        this.loginErrMsg = error.error.message;
        this.SpinnerService.hide();
      }
    );

  }
  handleError() {
    this.alertMsg = null;
  }
  forgotPwd() {
    if (this.forgotPwdForm.value.email) {
      this.forgetpasswordByEmail();
    }
    if (this.forgotPwdForm.value.mobile) {
      this.fogetPasswordByMobile();
    }



  }
  fogetPasswordByMobile() {

    const d1 = {
      mobile: this.forgotPwdForm.value.mobile
    }
    console.log("mobile", this.forgotPwdForm.value.mobile);

    this.RegisterServiceobj.forgetPswordForMobile(d1).subscribe((Response: any) => {
      console.log(" pwd Response:");
      console.log(Response);
      // alert(Response.message)
      if (Response.success) {

       
        this.otpToken = Response.token;
        this.otp = true;
        const m1 = JSON.stringify(this.forgotPwdForm.value.mobile);
        //   const m1 = '7276147078';
        let m2 = m1.substr(6);
        const m3 = "******" + m2;
        console.log(m3);
        this.OtpMsg = ""
        this.OtpMsg = " One Time Password has been sent on your mobile number ending" + m3;
        console.log(this.OtpMsg);

        //  this.mobileNo = "******" + m1;
        //  console.log(this.mobileNo);

        // this.alertMsg = Response.message;
      }

    }, (error) => {
      this.OtpMsg = "an error occured"
      console.log(error);

    })
  }
  forgetpasswordByEmail() {
    console.log("email:" + this.forgotPwdForm.value.email);
    this.RegisterServiceobj.forgotPassword(this.forgotPwdForm.value.email).subscribe(
      (Response: any) => {


        console.log(" pwd Response:");
        console.log(Response);
        console.log(Response.token);
        localStorage.setItem("token", Response.token)
        // alert(Response.message)
        if (Response.success) {
debugger;
          this.otpToken = Response.token;
          this.otp = true;
          const m1 = this.forgotPwdForm.value.email;
          const m2 = m1.substr(6);
          const m3 = "******" + m2;
          this.OtpMsg = "One Time Password has been sent on your email id ending " + m3;
          console.log(this.OtpMsg);

          // this.alertMsg = Response.message;
        }

      },
      (error: any) => {
        this.OtpMsg = "an error occured"
        //  this.alertMsg = "an error occured";
      });
  }
  resendOtp() {
    console.log("reset otp");
    if (this.forgotPwdForm.controls.mobileOrEmail.value == 'email') {
      this.resendOtpForEmail();
      console.log("reset otp email");
      this.otpresent=true;
    }
    if (this.forgotPwdForm.controls.mobileOrEmail.value == 'mobile') {
      this.resendOtpForMobile();
      console.log("reset otp mobile");
      this.otpresent=true;
    }
  }
  resendOtpForMobile() {
    //   // ;

    var mobile = this.forgotPwdForm.value.mobile
    const postData = {
      mobile: mobile
        }
    const token = localStorage.getItem("token");

    
    this.RegisterServiceobj.resendOtpForMobile(mobile, token).subscribe((res: any) => {
      // ;


    

    })
  }
  resendOtpForEmail() {
    // ;

    var email = this.forgotPwdForm.value.email

    const token = localStorage.getItem("token");
    this.RegisterServiceobj.resendOtpForEmail(email, token).subscribe((res: any) => {
      // ;
      console.log(res);

    })
  }
  submitOtp() {

    console.log("email:" + this.forgotPwdForm2.value.otp);
    this.RegisterServiceobj.submitOtp(this.forgotPwdForm2.value.otp, this.otpToken).subscribe(

      (Response: any) => {


        console.log(" otp Response:");
        console.log(Response);

        if (Response.status) {

          this.resetpwd = true;
          this.otp = false;
          localStorage.removeItem("token");
        }

      },
      (error: any) => {
        console.log(error.message);

        //  this.alertMsg = error.message;
      })

  }

  resetPassword() {
    console.log("email:" + this.forgotPwdForm3.value.password);
    this.RegisterServiceobj.resetPwd(this.forgotPwdForm3.value.password, this.otpToken).subscribe(
      (Response: any) => {

        console.log(" Reset Pwd Response:");
        console.log(Response);
        alert(Response.message)
        if (Response.status) {

          //   this.alertMsg = "password reset successfully";
          this.router.navigateByUrl('/home')
        }

      }),
      (error: any) => {

        //  this.alertMsg = "error occurred:";
        console.log(error);

      }

  }

  closeModal(){
   
    this.otp=false;
    this.resetpwd=false;
  }

}
