import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { VideoStreamService } from '../Services/video-stream.service';

@Component({
  selector: 'app-ordered-videos',
  templateUrl: './ordered-videos.component.html',
  styleUrls: ['./ordered-videos.component.scss']
})
export class OrderedVideosComponent implements OnInit {

    openslider: boolean = false;
  imgUrl: any = 'https://valart.s3.us-east-1.wasabisys.com/';
  orderedData:any[] = [];

  constructor(private videoService: VideoStreamService,  private router: Router) { 

  }

  ngOnInit(): void {
    this.getOrderdVideos();
  }

  clickEvent(event:any) {
 
    this.openslider = !this.openslider;
  }


    getOrderdVideos = () => {
      this.videoService.gevideoOrders().subscribe((res:any)=>{
        console.log("res : ",res);

        res.results.forEach((element:any) => {
          if(element.liveVideoIds.length > 0 || element.packageVideoIds.length > 0 || element.recorededVideoIds.length > 0) {
            this.orderedData.push(element);
          }      
        });
        console.log("Order Data : ", this.orderedData);
        
      },
      (err:any)=>{
        console.log("ERR : ",err);
      });
  }

  
  goToDetail(compName: string, id: any) {
    this.router.navigate(['/item-details', { compName, id }]);
  }


  cancelOrder = (id:any) =>{

    console.log("Cancel Order Id : ",id);

    this.videoService.cancelOrder(id).subscribe((res:any)=>{
      console.log("RES of Cancel Order : ",res);
      // this.getOrderdVideos();

      window.location.reload();
    },
    (err:any)=>{
      console.log("ERR : ",err);
    });


  }

}
 