import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { environment } from "../../environments/environment";
@Injectable({
  providedIn: 'root',
})
export class VideoStreamService {

  videoCount = new BehaviorSubject(0)

  headers = new HttpHeaders({
    'Content-Type': 'application/json; charset=utf-8',
  });
  storeId: any;
  constructor(private http: HttpClient) {
    var currentUser:any =  localStorage.getItem('currentUser');
    this. storeId=JSON.parse(currentUser);
    if(this. storeId){ 
      this.storeId=this.storeId.user.storeId;
    }
  }
  headers2 = new HttpHeaders({
    'Content-Type': 'application/json; charset=utf-8',
  });

  createVideos(body: any, type: string) {
    switch (type) {
      // case 'recordedVideo': {
      //   return this.http.post(
      //     `${environment.BASE_URL}/recordedvideoservices/create`,
      //     body
      //   );
      // }
      case 'liveVideo': {
        return this.http.post(
          `${environment.BASE_URL}/livevideoservices/create`,
          body
        );
      }
      case 'packageVideo': {
        return this.http.post(
          `${environment.BASE_URL}/packagevideoservices/create`,
          body
        );
      }
      default: {
        return this.http.post(
          `${environment.BASE_URL}/recordedvideoservices/create`,
          body
        );
      }
    }
  }

  getDataById(id: any, type:any) {
    console.log("ID :",id,"Type: ",type);
    
    switch (type) {
      case 'liveVideo': {
        return this.http.get(
          `${environment.BASE_URL}/livevideoservices/${id}`);
      }
      case 'packageVideo': {
        return this.http.get(
          `${environment.BASE_URL}/packagevideoservices/${id}`);
      }
      default: {
        return this.http.get(
          `${environment.BASE_URL}/recordedvideoservices/${id}`);
      }
    }
  }

  updateVideo(id: any, body: any, type: string) {
    //  // ;;
    switch (type) {
      // case 'recordedVideo': {
      //   return this.http.put(
      //     `${environment.BASE_URL}/recordedvideoservices/update/${id}`,
      //     body
      //   );
      // }
      case 'liveVideo': {
        return this.http.put(
          `${environment.BASE_URL}/livevideoservices/update/${id}`,
          body
        );
      }
      case 'packageVideo': {
        return this.http.put(
          `${environment.BASE_URL}/packagevideoservices/update/${id}`,
          body
        );
      }
      default: {
        return this.http.put(
          `${environment.BASE_URL}/recordedvideoservices/update/${id}`,
          body
        );
      }
    }
  }

  deleteVideo(type:any,id: any) {
    switch (type) {
      case 'liveVideo': {
        return this.http.delete(
          `${environment.BASE_URL}/livevideoservices/delete/${id}`
        );
      }
      case 'packageVideo': {
        return this.http.delete(
          `${environment.BASE_URL}/packagevideoservices/delete/${id}`
        );
      }
      default: {
        return this.http.delete(
          `${environment.BASE_URL}/recordedvideoservices/delete/${id}`
        );
      }
    }
  }

  getVideoByStoreId(type: string, storeId:any) {
    switch (type) {
      case 'liveVideo': {
        return this.http.get(
          `${environment.BASE_URL}/livevideoservices/store/${storeId}`
        );
      }
      case 'packageVideo': {
        return this.http.get(
          `${environment.BASE_URL}/packagevideoservices/store/${storeId}`
        );
      }
      default: {
        return this.http.get(
          `${environment.BASE_URL}/recordedvideoservices/store/${storeId}`
        );
      }
    }
  }

  // createVideoLabel(name: any) {
  //   const body = {
  //     storeId: this.storeId,
  //     subCatName: name,
  //   };
  //   return this.http.post(
  //     `${environment.BASE_URL}/subcategory/create`,
  //     body
  //   );
  // }

  // getVideoLabels() {
  //   return this.http.get(
  //     `${environment.BASE_URL}/subcategory/subcat-store/${this.storeId}`
  //   );
  // }

  //raser pay api

  razorPay(data:any){
    // ;
   var token:any =  localStorage.getItem('loginToken');
  // var token=JSON.parse(currentUser);
  //console.log("token in razorpay:"+token);
    //  var token=this.storeId.token;
   // var token=  localStorage.getItem('loginToken');
    const headers= new HttpHeaders({ 'Content-Type': 'application/json','Authorization': token});
    return this.http.post(`${environment.BASE_URL}/user/razorpay/create`,data,{headers:headers});
  }

  onPaymentSuccess(data:any){
    var currentUser:any =  localStorage.getItem('currentUser');
    this. storeId=JSON.parse(currentUser);
    var token=this.storeId.token;
    const headers= new HttpHeaders({ 'Content-Type': 'application/json','Authorization': token});
    return this.http.post(`${environment.BASE_URL}/user/razorpay/success`,data,{headers:headers});

  }

  getLatestVideoRecords(storeId:any){
    return this.http.get(
      `${environment.BASE_URL}/recordedvideoservices/latest/recorded/${storeId}`
    );
  }

  getOrderdVideos(){
    var currentUser:any =  localStorage.getItem('currentUser');
    this. storeId=JSON.parse(currentUser);
    var token=this.storeId.token;
    const headers= new HttpHeaders({ 'Content-Type': 'application/json','Authorization': token});
    return this.http.get(`${environment.BASE_URL}/order/video/order`,{headers:headers});
  }

  gevideoOrders(){
    var currentUser:any =  localStorage.getItem('currentUser');
    this. storeId=JSON.parse(currentUser);
    var token=this.storeId.token;
    const headers= new HttpHeaders({ 'Content-Type': 'application/json','Authorization': token});
    return this.http.get(`${environment.BASE_URL}/order/vorder-list`,{headers:headers});
  }



  cancelOrder(id:any){
    var currentUser:any =  localStorage.getItem('currentUser');
    this. storeId=JSON.parse(currentUser);
    var token=this.storeId.token;
    const headers= new HttpHeaders({ 'Content-Type': 'application/json','Authorization': token});
    // return this.http.put(`${environment.BASE_URL}/order/cancel/video-order/${id}`,{},{headers:headers});
    // let id = obj._id;
    // console.log("Order Id : ",id);
    
    return this.http.put(`${environment.BASE_URL}/order/cancel/video-order/${id}`,{},{headers:headers});

    // if(obj.liveVideoIds.length>0){
    //   return this.http.put(`${environment.BASE_URL}/order/cancel/video-live-order/${id}`,{},{headers:headers});
    // }else{
    //   return this.http.put(`${environment.BASE_URL}/order/cancel/video-order/${id}`,{},{headers:headers});
    // }
  }


  getVideoOrdersByStoreId = (storeId:any) =>{ 
    return this.http.get(`${environment.BASE_URL}/order/videos/store/${storeId}`);
  }

}
