import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { noConflict } from 'jquery';
import { NgxSpinnerService } from "ngx-spinner";
import { SuperAdminService } from 'src/app/Services/super-admin.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-add-address',
  templateUrl: './add-address.component.html',
  styleUrls: ['./add-address.component.scss']
})
export class AddAddressComponent implements OnInit {
  regForm!: FormGroup;
  submitted = false
  openslider: boolean = false;
  NoType = /^[0-9]{1,3}$/;
  pickupAddId: any;
  add1: any;
  addType = ["house no ", "flat no ", "rode no"]
  alertMsg: any;
  constructor(private router: Router, private SpinnerService: NgxSpinnerService, private superAdminService: SuperAdminService, private formBuilder: FormBuilder, private httpClient: HttpClient) { }

  ngOnInit(): void {
    this.regForm = this.formBuilder.group({
      // pickup_location: ['', Validators.required],
      name: ['', Validators.required],
      email: ['', [Validators.required, Validators.email]],
      phone: ['', [Validators.required, Validators.pattern("^((\\+91-?)|0)?[0-9]{10}$")]],
      address: ['', [Validators.required, Validators.minLength(10)]],
      address_2: ['', Validators.required],
      city: ['', Validators.required],
      state: ['', Validators.required],
      country: ['', Validators.required],
      pin_code: ['', [Validators.required, Validators.pattern('[0-9]{6}')]],
      addressType: ['', Validators.required],
      // numAddress: ['', [Validators.required, Validators.maxLength(3)]]
      numAddress: ['', [Validators.required, Validators.pattern(this.NoType)]]
    })
    this.getAddress();
  }
  stripText(event: any) {
    const seperator = '^([0-9])';
    const maskSeperator = new RegExp(seperator, 'g');
    let result = maskSeperator.test(event.key); return result;
  }

  get f() {
    return this.regForm.controls;
  }
  goToHome() {
    this.router.navigateByUrl("/admin/account");
  }
  getAddress() {

    //store/pickup-address/pickupaddressid
    this.SpinnerService.show();
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    var token = store.token;
    var storeId = store.user.storeId;
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': token });
    this.superAdminService.getStoreById(storeId).subscribe((res: any) => {
      console.log(res);
      this.SpinnerService.hide();
      this.pickupAddId = res[0].pickupAddress._id;
      console.log(this.pickupAddId);
      if (this.pickupAddId) {
        this.SpinnerService.show();
      }
      this.httpClient.get(environment.BASE_URL + "/store/pickup-address/" + this.pickupAddId, { headers: headers }).subscribe((res: any) => {
        console.log(res.data);
        const data = res.data;
        const type = data.shiprocket_pickup_address;
        const AddType = type.substr(0, 9);
        const AddType1 = AddType.trim()
        const no = type.substr(9, 3);

        const no1 = no.trim();
        // const addressLst = type.substr(14)
        const addressLst = type.substr(13);
        const addressLst1 = addressLst.trim();
        this.regForm.patchValue({
          name: data.shiprocket_pickup_name,
          email: data.shiprocket_pickup_email,
          phone: data.shiprocket_pickup_phone,
          // address: addressLst,
          address: addressLst1,
          address_2: data.shiprocket_pickup_address_2,
          city: data.shiprocket_pickup_city,
          state: data.shiprocket_pickup_state,
          country: data.shiprocket_pickup_country,
          addressType: AddType1,
          // numAddress: no
          numAddress: no1,
          pin_code: data.shiprocket_pickup_pincode
        })
        this.SpinnerService.hide();
        console.log(this.regForm.value);

      }, (error) => {
        console.log(error);
        this.SpinnerService.hide();

      })
    })

  }
  submit() {
    console.log(this.regForm);

    this.submitted = true;

    if (this.regForm.invalid) {
      console.log("form invalid");
      return

    }
    this.SpinnerService.show();
    const type = this.regForm.get('addressType')?.value;
    if (type.length === 7) {
      console.log(7);
      this.add1 = this.regForm.get('addressType')?.value + "   " +
        this.regForm.get('numAddress')?.value + " "
        + this.regForm.get('address')?.value;
      console.log("type: " + 10, "numAddress" + 14);

    } if (type.length === 8) {
      console.log(8);
      this.add1 = this.regForm.get('addressType')?.value + "  " +
        this.regForm.get('numAddress')?.value + "  "
        + this.regForm.get('address')?.value;
      console.log("type: " + 10, "numAddress" + 14);

    }

    // const add1 = this.regForm.get('addressType')?.value + " " +
    //   this.regForm.get('numAddress')?.value + " "
    //   + this.regForm.get('address')?.value;

    const postData = {
      pickup_location: this.regForm.get('pin_code')?.value,
      name: this.regForm.get('name')?.value,
      email: this.regForm.get('email')?.value,
      phone: this.regForm.get('phone')?.value,
      //  phone: JSON.stringify(this.regForm.get('phone')?.value),
      // address: this.regForm.get('address')?.value,
      address: this.add1,
      city: this.regForm.get('city')?.value,
      address_2: this.regForm.get('address_2')?.value,
      state: this.regForm.get('state')?.value,
      country: this.regForm.get('country')?.value,
      pin_code: this.regForm.get('pin_code')?.value,
    }
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    var token = store.token;
    var storeId = store.user.storeId;
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': token });
    console.log(postData);
    const url = environment.BASE_URL + "/store/update/pickup-address/" + storeId;

    this.httpClient.put(url, postData, { headers: headers }).subscribe((res: any) => {
      console.log(res);

      this.SpinnerService.hide();
      if (this.pickupAddId) {
        this.alertMsg = "address updates sucessfully";
      } else {
        this.alertMsg = "address updates sucessfully... admin can verify store..";
      }
      this.submitted = false;
      localStorage.setItem("storeStatus", "false");
      this.regForm.reset();
      this.router.navigate(['/admin/account']);

    }, (error) => {
      console.log(error);

      this.SpinnerService.hide();
    })

  }
  handleError() {
    this.alertMsg = null;
  }
  clickEvent(event: any) {
    this.openslider = !this.openslider;
  }

}
