import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SuperAdminService } from 'src/app/Services/super-admin.service';
import { DataService } from '../../../Services/data.service';
import { ValstoreRegisterService } from '../../../Services/valstore-register.service';
@Component({
  selector: 'app-control-panel',
  templateUrl: './control-panel.component.html',
  styleUrls: ['./control-panel.component.scss']
})
export class ControlPanelComponent implements OnInit {
  ownerName: any;
  storeNames: any = [];
  storeList: any;
  storeId: any;
  currentUser: any;
  storeName: any;
  storeType: any;
  userName: any;
  storeDetails: any;
  storeLogo: any;
  imageURL = "";
  currentStoreId: any;
  byzcode: any;
  videoFiles2: any = [];
  constructor(private router: Router,
    private superAadminServices: SuperAdminService, private ValstoreRegisterServiceObj: ValstoreRegisterService, private data: DataService) {
    var storedData: any = localStorage.getItem('currentUser');
    //  // ;
    storedData = JSON.parse(storedData);
    this.ownerName = storedData.user.ownerName;
    this.storeDetails = this.ValstoreRegisterServiceObj.getStoreDetail().toPromise();
    this.data.setStoreDetails(this.storeDetails);
    this.ValstoreRegisterServiceObj.getListOfStore().subscribe((response: any) => {

      //  console.log(response);
      const storeId = response[0]._id;
      localStorage.setItem("storeId", storeId);

      this.storeList = response;
      this.storeList.forEach((element: any) => {
        this.storeNames.push(element.name);
      });
    });
    this.getStoreDetails();
  }
  async ngOnInit() {


    this.storeDetails = await this.ValstoreRegisterServiceObj.getStoreDetail().toPromise();

    this.storeName = this.storeDetails[0].name;
    this.storeLogo = this.storeDetails[0].storeImagesId.storeLogo;
    if (this.storeDetails[0].storeImagesId.storeLogo) {
      this.imageURL = "https://s3.wasabisys.com/valart/t_".concat(this.storeLogo);
    }
    if (!this.storeDetails[0].storeImagesId.storeLogo) {
      this.imageURL = "/assets/images/noImg.png"
    }
    this.getUserById();


  }
  getUserById() {
    this.superAadminServices.getUserById(this.storeDetails[0].userId).subscribe((res: any) => {

      this.byzcode = res.message[0].code;

    })
  }
  personalInfo() {
    this.router.navigateByUrl('/admin/personalinfo');
  }
  getStoreDetails() {
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    this.currentStoreId = store.user.storeId;
    console.log(this.currentStoreId);


  }
  setStoreId(storeName: any) {


    const s1 = this.storeList.filter((x: any) => x.name === storeName);
    console.log(s1);
    const data = {
      storeName: s1[0].name,
      logo: s1[0].storeImagesId.storeLogo
    }
    console.log(data);
    localStorage.setItem("storeDetails", JSON.stringify(data));
    localStorage.setItem("storeStatus", s1[0].verified)

    var storeType;
    this.storeList.forEach((element: any) => {

      if (element.name == storeName) {
        this.storeId = element._id;
        storeType = element.storeType;

        this.storeName = element.name;
      }
    });
    this.currentUser = localStorage.getItem('currentUser');
    this.currentUser = JSON.parse(this.currentUser);
    var name = this.currentUser.user.ownerName.split('');
    this.userName = name[0];
    console.log(this.userName);
    this.currentUser.user.storeId = this.storeId;
    this.currentUser.user.storeName = this.storeName;
    localStorage.setItem("currentUser", JSON.stringify(this.currentUser));
    this.storeType = localStorage.getItem('storeType');
    this.storeType = storeType;
    localStorage.setItem('storeType', this.storeType);
    
    document.getElementById('closeModal')?.click();
   // window.location.reload();
    this.router.navigate(['/admin/dashboard']);
  }

  Createstore() {
    this.router.navigate(['/regvalstore']);
    document.getElementById('closeModal')?.click();

  }
  Logout() {
    // localStorage.removeItem('currentUser');
    // localStorage.removeItem('storeid');
    localStorage.clear();
    this.router.navigate(['/']);

  }

}
