import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
//import { FormControl, FormGroup } from '@angular/forms';
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { environment } from 'src/environments/environment';
import { LoginComponent } from '../login/login.component';
import { DataService } from '../Services/data.service';
import { RegisterService } from '../Services/register.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss'],
})
export class RegisterComponent implements OnInit {
  pwdPattern: boolean = false;
  mobPattern = '^[0-9]{10}$';
  moberror: boolean = false;
  mobNoValidateStatus2 = false;
  alertMsg: any;
  submitted = false;
  month = [];
  loginErrMsg: any;
  loginErr = false;
  showPass: boolean = false;
  mobileNoExist = '';
  RegisterForm = new FormGroup({
    fullname: new FormControl(),
    mobno: new FormControl(),
    email: new FormControl(),
    password: new FormControl(),
    confirmpassword: new FormControl(),
    pincode: new FormControl(),
    gender: new FormControl(),
    Dob: new FormControl(),
    city: new FormControl(),
    state: new FormControl(),
    describesyou: new FormControl(),
  });

  showPassword: boolean = false;
  showPassword2: boolean = false;
  password2: any = '';
  password: any = '';
  mobileNoError = false;
  Loginform!: FormGroup;
  constructor(
    private data: DataService,
    private loginobj: LoginComponent,
    private http: HttpClient,
    private SpinnerService: NgxSpinnerService,
    private fb: FormBuilder,
    private RegisterServiceobj: RegisterService,
    public router: Router
  ) {}

  ngOnInit(): void {
    window.scroll(0, 0);
    this.RegisterForm = new FormGroup({
      fullname: new FormControl('', [Validators.required]),
      mobno: new FormControl('', [Validators.required]),
      email: new FormControl('', [Validators.required, Validators.email]),
      password: new FormControl('', [
        Validators.required,
        Validators.pattern(
          '(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[$@$!%*?&])[A-Za-zd$@$!%*?&].{8,}'
        ),
      ]),
      confirmpassword: new FormControl('', [
        Validators.required,
        Validators.pattern(
          '(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[$@$!%*?&])[A-Za-zd$@$!%*?&].{8,}'
        ),
      ]),
      pincode: new FormControl(''),
      gender: new FormControl('', [Validators.required]),
      dob: new FormControl('', [Validators.required]),
      city: new FormControl('', [Validators.required]),
      state: new FormControl('', [Validators.required]),
      describesyou: new FormControl(''),
    });
    this.showPassword = false;

    this.Loginform = this.fb.group({
      email: ['', [Validators.required, Validators.email]],
      password: ['', Validators.required],
    });
  }
  get f() {
    return this.Loginform.controls;
  }

  toggleShowPassword() {
    this.showPassword = !this.showPassword;
  }

  toggleShowPassword2() {
    this.showPassword2 = !this.showPassword2;
  }

  toogleshowPass() {
    this.showPass = !this.showPass;
  }
  existingUser() {
    this.submitted = true;
    if (this.Loginform.invalid) {
      return;
    }
    this.SpinnerService.show();
    const body = {
      email: this.Loginform?.get('email')?.value,
      password: this.Loginform?.get('password')?.value,
    };
    console.log(body);

    this.RegisterServiceobj.existingUserMakeStoreAdmin(body).subscribe(
      (res: any) => {
        this.submitted = false;
        console.log(res.data);
        this.SpinnerService.hide();
        if (
          res.message ===
          'Now you are registered as a store admin, Please wait for your account verification. '
        ) {
          this.alertMsg = res.message;
          this.router.navigate(['/home']);
          alert(res.message);
        }
        if (res.message === 'User with this email id does not exits.') {
          this.alertMsg = res.message;
        }
        if (res.message === 'Email or password is incorrect. ') {
          this.alertMsg = res.message;
        }
        if (
          res.message ===
          'You are registered as a store admin , please wait for our team to verify your account.'
        ) {
          this.alertMsg = res.message;
          alert(
            'Your request has been submitted, our team will get back to you soon'
          );
          this.router.navigate(['/home']);
        }

        //  this.alertMsg = res.message;
      },
      (error) => {
        this.submitted = false;
        this.SpinnerService.hide();
        console.log(error);
      }
    );
  }
  Signup() {
    const mobileNo = this.RegisterForm.value.mobno;
    if (this.mobileNoError === true) {
      this.alertMsg = 'mobile no is exist';
      return;
    }
    this.SpinnerService.show();

    console.log(this.RegisterForm.value);
    this.data.setMobNo(mobileNo);
    this.data.setEmail(this.RegisterForm.value.email);
    this.SpinnerService.show();
    this.RegisterServiceobj.Register(
      this.RegisterForm.value.fullname,
      this.RegisterForm.value.mobno,
      this.RegisterForm.value.email,
      this.RegisterForm.value.password,
      this.RegisterForm.value.city,
      this.RegisterForm.value.state,
      this.RegisterForm.value.pincode,
      this.RegisterForm.value.gender,
      this.RegisterForm.value.describesyou
    ).subscribe(
      (response: any) => {
        console.log('response:' + response);
        localStorage.setItem('currentUser', JSON.stringify(response));
        this.router.navigate(['/otpvalidation']);
        this.SpinnerService.hide();
        // alert("Your request has been submitted, our team will get back to you soon")
      },
      (error: any) => {
        this.SpinnerService.hide();
        this.alertMsg = error.error.message;
        //  alert(error.error.message)
        this.router.navigate(['/register']);
        window.location.reload();
      }
    );
  }
  agenValidateMob(postData: { mobile: any }) {
    this.mobNoValidateStatus2 = false;
    this.http
      .post(environment.BASE_URL + '/register/check-mobile', postData)
      .subscribe((res: any) => {
        if (res.message === 'Mobile Number Already exists') {
          this.alertMsg = 'Mobile Number Already exists';
          this.mobNoValidateStatus2 = true;
        }
      });
  }

  handleError() {
    this.alertMsg = null;
  }
  stripText(event: any) {
    const seperator = '^([0-9])';
    const maskSeperator = new RegExp(seperator, 'g');
    let result = maskSeperator.test(event.key);

    return result;
  }
  getNo(values: any) {
    if (values.length === 10) {
      console.log(values.length);
      // this.ValidateMobileNoServerSide(values);
    }
  }
  openLogin() {
    this.loginobj.openModal();
  }

  checkPwd(pwd: any) {
    //  // ;
    var regexp = RegExp(
      '^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$'
    );
    var test = regexp.test(pwd);
    if (!test) {
      this.pwdPattern = true;
    } else {
      this.pwdPattern = false;
    }
  }

  validateMob(mobno: any) {
    //  // ;
    var regexp = RegExp('^[6-9][0-9]{9}$');
    var test = regexp.test(mobno);
    if (!test) {
      this.moberror = true;
    } else {
      this.moberror = false;
      //   this.ValidateMobileNoServerSide(mobno);
    }
  }
  // ValidateMobileNoServerSide(mobno: any) {
  //   console.log(mobno);
  //   const postData = {
  //     mobile: mobno
  //   }
  //   this.http.post(environment.BASE_URL + "/register/check-mobile", postData).subscribe((res: any) => {
  //     console.log(res);
  //     if (res.message === "Good to Go!") {
  //       this.mobileNoError = false
  //       this.mobileNoExist = res.message
  //     }
  //     if (res.message === "Mobile Number Already exists")
  //       this.mobileNoError = true
  //     this.mobileNoExist = res.message
  //   })

  // }
}
