import { Location } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-rec-video',
  templateUrl: './rec-video.component.html',
  styleUrls: ['./rec-video.component.scss']
})
export class RecVideoComponent implements OnInit {

  videoSource:any
  url:any = 'https://valart.s3.us-east-1.wasabisys.com/'
  anotherId:any

  constructor(private router : Router, private location: Location, private actRoute:ActivatedRoute) { }

  ngOnInit(): void {
    let src = this.actRoute.snapshot.paramMap.get('src');
    this.videoSource = this.url + src;
  }

  toggleVideo(){}

  goBack() {
    this.location.back();
  } 

  msgConnect(){

  }

}
