import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { VideoStreamService } from '../Services/video-stream.service';

@Component({
  selector: 'app-my-videos',
  templateUrl: './my-videos.component.html',
  styleUrls: ['./my-videos.component.scss']
})
export class MyVideosComponent implements OnInit {
  openslider: boolean = false;
  imgUrl: any = 'https://valart.s3.us-east-1.wasabisys.com/';
  recordedData:any[] = [];
  liveSessionData:any[] = [];
  packageData:any[] = [];
  constructor(private videoService: VideoStreamService,  private router: Router,) { }

  ngOnInit(): void {
    this.getOrderdVideos();
  }

  getOrderdVideos(){
      this.videoService.getOrderdVideos().subscribe((res:any)=>{
        console.log("res : ",res);
        console.log(res.results.packageVideoIds.length);
        
        this.packageData = res.results.packageVideoIds;
        this.liveSessionData = res.results.liveVideoIds;
         this.recordedData = res.results.recorededVideoIds;
        
      },
      (err:any)=>{
        console.log("ERR : ",err);
      });
  }
 // open mobile slider
 clickEvent(event:any) {
 
  this.openslider = !this.openslider;
}


  goToDetail(compName: string, id: any) {
    this.router.navigate(['/item-details', { compName, id }]);
  }
}
