  generateCalender(selectedDate: any) {

    const weekDays = [
      'Sunday',
      'Monday',
      'Tuesday',
      'Wednesday',
      'Thrusday',
      'Friday',
      'Saturday'
    ]
    switch (this.scheduleRepeats) {
      case 'Daily': {
        if (selectedDate >= this.startDate && selectedDate <= this.endDate) {
          this.days = [selectedDate];
          this.times = this.selectedSchedule[0].schtime;
        }
        break;
      }
      case 'Weekly': {
        console.log("selected week : ", selectedDate.getDay());
        let weekName = ''
        weekName = weekDays[selectedDate.getDay() || 0] || 'Sunday';
        let res = this.selectedSchedule.findIndex(
          (element) => element.weekDay === weekName
        );
        if (res != -1) {
          // this.days = [this.selectedSchedule[res].weekDay];
          this.days = [selectedDate];
          this.times = this.selectedSchedule[res].schtime;
        } else {
          this.days = [selectedDate];
          this.times = []
        }
        break;
      }
      case 'Monthly': {
        let date = JSON.stringify(selectedDate.getDate());
        console.log("date : = ", date)
        let res = this.selectedSchedule.findIndex(
          (element) => element.weekDay === date
        );
        if (res != -1) {
          // this.days = [this.selectedSchedule[res].weekDay];
          this.days = [selectedDate];
          this.times = this.selectedSchedule[res].schtime;
        } else {
          this.days = [selectedDate];
          this.times = []
        }
        break;
      }
      default: {
        let res = this.selectedSchedule.findIndex(
          (element) => element.weekDay.getTime() === selectedDate.getTime()
        );
        if (res != -1) {
          this.days = [this.selectedSchedule[res].weekDay];
          this.times = this.selectedSchedule[res].schtime;
        } else {
          // this.days = [this.selectedSchedule[1].weekDay];
          // this.times = this.selectedSchedule[1].schtime;
          this.days = [selectedDate];
          this.times = []
        }
        break;
      }
    }
    this.calenderData[this.days[0]]
    for (let i = 0; i < this.days.length; i++) {
      this.calenderData[this.days[i]] = [];
      for (let j = 0; j < this.times.length; j++) {
        this.calenderData[this.days[i]].push(this.times[j]);
      }
    }
    console.log("calenderData : ", this.calenderData);
  }

