import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ScheduleService } from '../Services/schedule.service';
import { VideoStreamService } from '../Services/video-stream.service';
import { NgxSpinnerService } from "ngx-spinner";
import { MatDialog } from '@angular/material/dialog';
import 'lodash';
declare var _:any;
@Component({
  selector: 'app-store-details',
  templateUrl: './store-details.component.html',
  styleUrls: ['./store-details.component.scss'],
})
export class StoreDetailsComponent implements OnInit {
  @ViewChild("favDialog",{static:false}) favDialog: any;
  @ViewChild('bestsellers') public bestsellers!: ElementRef;
  @ViewChild('livesessions') public livesessions!: ElementRef;
  @ViewChild('recordedVideos') public recordedVideos!: ElementRef;
  @ViewChild('packages') public packages!: ElementRef;
  @ViewChild('goHomeUrl') goHomeUrl!: ElementRef<HTMLElement>;
  @ViewChild('goToStoreReview') goToStoreReview!: ElementRef<HTMLElement>;
  @ViewChild('goToStoreMessage') goToStoreMessage!: ElementRef<HTMLElement>;
  dialogmsg:any;
  storeData: any;
  paramkey:any;
  storeId: any;
  storeLogo: any;
  storeImages: any;
  recordedVideo: any;
  liveVideo: any;
  
  packageVideo: any;
  BestSellarVideos:any;
  selectedTab:string = 'bestsellers' 
  imgUrl: any = 'https://valart.s3.us-east-1.wasabisys.com/';
  imgUrl1:any= 'https://valart.s3.us-east-1.wasabisys.com/t_';
  storeVideoFiles: any[] = [];
  favFlag:boolean = false;
  favoriteId : any
  reviewCount:number = 0;
  patronCount:number = 0;
  customerCount:number = 0;
  reviewUrl:any
  favProductData:any[] = [];
  constructor(
    private scheduleService: ScheduleService,
    private videoStreamService: VideoStreamService,
    private router: Router,
    private SpinnerService: NgxSpinnerService,
    private actRoute:ActivatedRoute,
    private dialog: MatDialog
  ) {
    // this.scheduleService.pageName.next('Store');
  }

  ngOnInit(): void {
    this.storeId = this.actRoute.snapshot.paramMap.get('id');
   this.paramkey= this.actRoute.snapshot.paramMap.get('key')
    localStorage.setItem('storeId',this.storeId);
        this.reviewUrl = `https://homebyze.netlify.app/Reviews/Store/${this.storeId}`;
    this.storeData = {};
    this.SpinnerService.show();
    this.initialization();
    if(localStorage.getItem('currentUser')){
      this.getFavoriteStoreList(); 
      this.getFavoriteProductList();
      this.getAllReview();
      this.getPatrons();
      this.getCustomes();
    }
  }

 async getFavoriteStoreList(){
    let storeId = this.storeId;

   var res:any= await this.scheduleService.fetchFavStore().toPromise();
      // console.log("fav store lsit : ",res.message);
      if(res.message.length > 0){
       
      let index = _.findIndex(res.message, function(o:any) { return o.storeId._id == storeId ; });
      if(index >= 0){
       
        this.favoriteId = res.message[index]._id
        this.favFlag = true;
      }
      }
   
  }

  getFavoriteProductList = () =>{

    this.scheduleService.fetchFavProducts().subscribe((res:any)=>{
      // console.log("fav store lsit : ",res.message);
      if(res.message.length > 0){
        this.favProductData = res.message ; 
      }
    },
    (err:any)=>{
      // console.log("ERR : ",err);
    })
  }

  getAllReview = () =>{
    this.scheduleService.fetchAllReviewByStoreId(this.storeId).subscribe((res:any)=>{
      if(res.message){
      this.reviewCount = res.message.length
      }
    },
    (err:any)=>{
      // console.log("ERR : ",err);
    })
  }
  getPatrons = () =>{
    this.scheduleService.fetchPatrons(this.storeId).subscribe((res:any)=>{
      if(res.message){
      this.patronCount = res.message.length
      }
    },
    (err:any)=>{
      // console.log("ERR : ",err);
    })
  }
  getCustomes = () =>{
    this.scheduleService.fetchCustomers(this.storeId).subscribe((res:any)=>{
      // console.log("reviews : ",res.message);
      if(res.data){
        this.customerCount = res.data.userId.length
      }
    },
    (err:any)=>{
      // console.log("ERR : ",err);
    })
  }



  goToLocation(location: string) {
    this.selectedTab = location;
    switch (location) {
      case 'bestsellers': {
        // console.log('location : ', location);
        this.bestsellers.nativeElement.scrollIntoView({
          behavior: 'smooth',
          block: 'end',
          inline: 'start',
        });
        break;
      }
      case 'livesessions': {
        this.livesessions.nativeElement.scrollIntoView({
          behavior: 'smooth',
          block: 'end',
          inline: 'start',
        });
        break;
      }
      case 'recordedVideos': {
        // console.log('location : ', location);
        this.recordedVideos.nativeElement.scrollIntoView({
          behavior: 'smooth',
          block: 'end',
          inline: 'start',
        });
        break;
      }
      case 'packages': {
        // console.log('location : ', location);
        this.packages.nativeElement.scrollIntoView({
          behavior: 'smooth',
          block: 'end',
          inline: 'start',
        });
        break;
      }
      default: {
        break;
      }
    }
  }

  initialization() {
    this.getStoreDetails();
    // this.getvideoLables();
    this.getVideoData();
  }

  getStoreDetails() {
    // ;
    this.scheduleService
      .getStoreById(this.storeId,this.paramkey)
      .subscribe((response: any) => {
        this.SpinnerService.hide();
        
        this.storeData = response.data[0];
        this.storeLogo = this.imgUrl+'t_'+ this.storeData?.storeImagesId.storeLogo;
        this.storeImages = this.imgUrl +'l_' +this.storeData?.storeImagesId.storeImages[0];
          this.storeData?.storeImagesId.storeVideos.forEach((element: any) => {
            // console.log(element);
            this.storeVideoFiles.push(`https://s3.wasabisys.com/valart/${element}`);
           
          });
          // ;
          localStorage.setItem('loginToken',response.token);
          localStorage.setItem('userName',response.name);
      },
      (err:any)=>{
        this.SpinnerService.hide();
      });
  }

  getVideoData() {
     ;
    this.videoStreamService
      .getVideoByStoreId('recordedVideo',this.storeId)
      .subscribe((response: any) => {
        // console.log(' recordedVideo :', response.data);
       
        this.recordedVideo = response.data;
        this.recordedVideo.forEach((element:any) => {
         
         var desc= element.description.split(" ");
         if(desc.length>15){
          
          element.desc=element.description.split(" ").splice(0,15).join(" ");
          element.moreFlag=true;
        }
        else{
         
          element.moreFlag=false;
        }
        });
        console.log(this.recordedVideo);
      //   var desc=response.data[0].description;
      //  var lines = desc.split(" "); 
      //   console.log(lines.length);

      });

    this.videoStreamService
      .getVideoByStoreId('liveVideo',this.storeId)
      .subscribe((response: any) => {
        // console.log(' liveVideo :', response.data);
        
        this.liveVideo = response.data;
        this.liveVideo.forEach((element:any) => {
         
         var desc= element.description.split(" ");
         if(desc.length>15){
          
          element.desc=element.description.split(" ").splice(0,15).join(" ");
          element.moreFlag=true;
        }
        else{
         
          element.moreFlag=false;
        }
      });
      });

    this.videoStreamService
      .getVideoByStoreId('packageVideo',this.storeId)
      .subscribe((response: any) => {
        
        // console.log(' packageVideo :', response);
        this.packageVideo = response.data;
        this.packageVideo.forEach((element:any) => {
         
         var desc= element.description.split(" ");
         if(desc.length>15){
          
          element.desc=element.description.split(" ").splice(0,15).join(" ");
          element.moreFlag=true;
        }
        else{
         
          element.moreFlag=false;
        }
      });
      });


      this.videoStreamService.getLatestVideoRecords(this.storeId)
      .subscribe((response: any) => {
        ;
        this.BestSellarVideos = response.data;
      });
  }

  goToDetail(compName: string, id: any) {
    this.router.navigate(['/item-details', { compName, id }]);
  }


 async addStoreTofav() {

    if(localStorage.getItem('currentUser')){
      this.SpinnerService.show();
      if(!this.favFlag){
       
    var res:any= await this.scheduleService.addStoreToFav(this.storeId).toPromise();
          this.SpinnerService.hide();
          this.favFlag = true;
          // alert("add to favorite successfully");
          this.dialogmsg = "Store has been added to your favourite list";
          this.dialog.closeAll();
          this.dialog.open(this.favDialog);
          
       
      }else{
      await this.getFavoriteStoreList();
     
      await  this.scheduleService.deleteFavStore(this.favoriteId).toPromise();
          this.SpinnerService.hide();
          
          // alert("remove from favorite successfully");
          this.dialogmsg = "Store has been removed from your favourite list ";
          this.dialog.closeAll();
          this.dialog.open(this.favDialog);
          this.favFlag = false;
         
       
      }
    }else{
      let el: HTMLElement = this.goHomeUrl.nativeElement;
      el.click();
    }
   // window.location.reload();
  }


  checkFavProduct = (productId:any):boolean =>{
   
      let index = _.findIndex(this.favProductData, function(o:any) { return (o.recordedVideoService && o.recordedVideoService._id == productId)|| (o.liveVideoService && o.liveVideoService._id == productId) || (o.packageVideoService && o.packageVideoService._id == productId) });
      if(index >= 0){
       
        // console.log("Index : ",index);
        return true
      }else{
       

        return false;
      }
  }


 async addProductTofav(productId:any) {
  
    if(localStorage.getItem('loginToken')){
      let index = _.findIndex(this.favProductData, function(o:any) { return (o.recordedVideoService && o.recordedVideoService._id == productId)|| (o.liveVideoService && o.liveVideoService._id == productId) || (o.packageVideoService && o.packageVideoService._id == productId) });
    //  console.log(index);
    this.SpinnerService.show();
      if(index >= 0){
      var res:any= await this.scheduleService.deleteFavProduct(this.favProductData[index]._id).toPromise();
      
      this.SpinnerService.hide();
          this.getFavoriteProductList();
          // alert("remove from favorite successfully");
          this.dialogmsg = "Product has been removed from your favourite list  ";
          this.dialog.closeAll();
          this.dialog.open(this.favDialog);
        // },(err:any)=>{
        //   this.SpinnerService.hide();
        //   // console.log("ERR : ",err);
        // });
      }else{
      var res:any=await this.scheduleService.addProductTofav(productId).toPromise();
     
          this.SpinnerService.hide();
          this.getFavoriteProductList();
          // alert("add to favorite successfully");
          this.dialogmsg = "Product has been added to your favourite list ";
          this.dialog.closeAll();
          this.dialog.open(this.favDialog);
        // },(err:any)=>{
        //   this.SpinnerService.hide();
        //   // console.log("ERR : ",err);
        // });
      }
    }else{
      let el: HTMLElement = this.goHomeUrl.nativeElement;
      el.click();
    }
  }

  goToReview = () => {
    // this.router.navigate(['/store-review',this.storeId]);
    let el: HTMLElement = this.goToStoreReview.nativeElement;
    el.click();
  }

  goToMessage = () =>{
    let el: HTMLElement = this.goToStoreMessage.nativeElement;
    el.click();
  }

  showMore(object:any){
    object.moreFlag=false;

  }
}
