import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VideoStreamService } from '../Services/video-stream.service';

@Component({
  selector: 'app-schedule-list',
  templateUrl: './schedule-list.component.html',
  styleUrls: ['./schedule-list.component.scss']
})
export class ScheduleListComponent implements OnInit {

  videoLabels:any=[];
  storeId:any;
  constructor(private videoStreamService: VideoStreamService,private router:Router) {
    var currentUser:any =  localStorage.getItem('currentUser');
    this. storeId=JSON.parse(currentUser);
    if(this. storeId){ 
      this.storeId=this.storeId.user.storeId;
    }
   }

  ngOnInit(): void {
    this.initialization();
  }

  initialization(){
    this.getvideoLables();
    this.getVideoData();
  }

  getvideoLables(){
    this.videoLabels = [
      {id:1, name:'Recorded Videos', value:'recordedVideos', display:false, data:[]},
      {id:2, name:'Live Sessions', value:'liveSessionVideos', display:false, data:[] },
      {id:3, name:'Packages', value:'packageVideos', display:false, data:[]}
    ]  
  }

  getVideoData(){
    this.videoStreamService.getVideoByStoreId('recordedVideo',this.storeId).subscribe((response:any)=>{
      console.log(" recordedVideo :", response.data);
      this.videoLabels[0].data = response.data;
    });

    this.videoStreamService.getVideoByStoreId('liveVideo',this.storeId).subscribe((response:any)=>{
      console.log(" liveVideo :", response.data);
      this.videoLabels[1].data = response.data;
    });

    this.videoStreamService.getVideoByStoreId('packageVideo',this.storeId).subscribe((response:any)=>{
      console.log(" packageVideo :", response.data);
      this.videoLabels[2].data = response;
    });
  }

  toggle(id:any){
    this.videoLabels.forEach((item:any)=>{
    if(item.id==id){
      item.display=!item.display;
    }
  });
  }

  onDetailClick(id:any){
    console.log("on detail Click");
    this.router.navigate(['/schedule',id]);

  }

  addToCart(id:any){

  }

}
