import { Location } from '@angular/common';
import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { PubNubAngular } from 'pubnub-angular2';
import { AdminService } from '../Services/admin.service'
import { ScheduleService } from '../Services/schedule.service';
@Component({
  selector: 'app-live-stream',
  templateUrl: './live-stream.component.html',
  styleUrls: ['./live-stream.component.scss']
})
export class LiveStreamComponent implements OnInit {


  @ViewChild('myVideo') myVideo!: any;
  playBackUrl:any;
  slotId:any;
  previousUrl!: string;
  storeId:any;
  imgUrl: any = 'https://valart.s3.us-east-1.wasabisys.com/';
  msgForm!: FormGroup;
  
  pubnub!: PubNubAngular;
  userChannel: any;
  myChannel: any;
  allMessage: any = [];
  chatBox: any = [];
  mainRom: any = [];
  FeedbackData: any = [];
  RequestData: any = [];
  suggesionData: any = [];
  userList:any[] = [];
  userInfo:any;
  
  constructor(private router: Router,private location: Location,  private formsBuilder: FormBuilder,
     private elementRef: ElementRef, private actRoute:ActivatedRoute,  pubnubObj: PubNubAngular,
     private adminService: AdminService,private SpinnerService: NgxSpinnerService, ) { 
      console.log("location : : ",location);
      this.getUserDetails();
      this.pubnub = pubnubObj;
      pubnubObj.init({
        publishKey: 'pub-c-05093612-76ee-4cbd-84f5-b132cc43a93b',
        subscribeKey: 'sub-c-fcf68878-beaf-11eb-8415-662615fc053c'
      });
      this.getMainRoomHistory();
      this.fetChMessage();
      this.getStoreQuery();
   }

  ngOnInit(): void {
    
    if(localStorage.getItem('usersData')){
      this.userList = JSON.parse(localStorage.getItem('usersData')!);
      console.log("UserList : ",this.userList);
    }
    // ;
    this.playBackUrl = this.actRoute.snapshot.paramMap.get('playBackUrl');
    this.slotId = this.actRoute.snapshot.paramMap.get('slotId');

    this.myChannel = this.slotId;

    if (this.pubnub) {
      this.pubnub.subscribe({
        channels: [this.myChannel],
      })
      this.pubnub.addListener({
        message: (m: any) => {
          const tempMain = {
            id: m.message.id,
            author: m.publisher,
            content: m.message.content,
            timetoken: m.timetoken,
            time: m.message.time,
            message: m.message,
            userName: "demo user",
            imageUrl: "assets/images/Patron/image-83.png",
            msgStatus: "active"
          }
          // this.mainRom.push(tempMain);
          console.log(m);
          const newMsg = {
            id: m.message.id,
            author: m.publisher,
            content: m.message.content,
            timetoken: m.timetoken,
            time: m.message.time,
            message: m.message,
            image: m.message.user.profileImageId ? m.message.user.profileImageId.image : '',
          }
          console.log(newMsg);
          this.chatBox.push(newMsg);
          console.log("CBOS : ",this.chatBox)
        }
      })
    }

    console.log("playBackUrl : ",this.playBackUrl);

    this.msgForm = this.formsBuilder.group({
      msgField: ['', Validators.required]
    })
  }

  getUserDetails() {
    this.adminService.getUser().subscribe((res:any)=>{
      console.log("USER INFO : ",res);
      this.userInfo = res.message;
    },
    (err:any)=>{
      console.log("ERR: ",err)
    });




    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    this.storeId = store.user.storeId;
    if(store.user.email != ""){
      this.userChannel = store.user.email;
    }else if(store.user.username != ""){
      this.userChannel = store.user.username;
    }else{
      this.userChannel = store.user.ownerName;
    }

  }

  getMainRoomHistory() {
    // ;
    this.pubnub.subscribe({
      channels: [this.myChannel],
    });
    this.pubnub.fetchMessages(
      {
        // channels: [this.myChannel],
        channels: [this.myChannel],
        count: 100,
        end: 16221986217189860
      },
      (status, response) => {
        const data = response.channels[this.myChannel]
        for (let i of data) {
          this.mainRom.push(
            {
              id: i.message.id,
              author: i.uuid,
              content: i.message.content,
              timetoken: i.timetoken,
              time: i.message.time,
              message: i.message,
              role: ""
            }
          )
        }
        console.log(this.mainRom);
      }
    )
  }


  fetChMessage() {
    let date = new Date().getTime();
    // console.log('end----', date * 10000);
    let end = date * 10000;
    // let data = end.toString();
    // console.log('data :', data);
    this.pubnub.fetchMessages(
      {
        channels: [this.myChannel],
        count: 100,
        end: 16221986217189860
      },
      (status, response) => {
        console.log('HISTOREY');
        console.log(response);
        let user1 = response.channels;
        let user1Data: any[] = [];
        console.log('user1', user1);
        for (var key in user1) {
          if (user1.hasOwnProperty(key)) {
            for (let val = 0; val < user1[key].length; val++) {
              //   console.log('baaba', user1[key][val].timetoken);
              user1Data = [...user1Data,
              {
                id: user1[key][val].message.id,
                author: user1[key][val].uuid,
                content: user1[key][val].message.content,
                timetoken: user1[key][val].timetoken,
                time: user1[key][val].message.time,
                message: user1[key][val].message,
                image: user1[key][val].message.user.profileImageId ? user1[key][val].message.user.profileImageId.image : '',
                role: ""
              }];
            }
            let tempdata = user1Data;
            for (let i = 0; i < tempdata.length - 1; i++) {
              for (let j = 0; j < tempdata.length - 1; j++) {
                if (tempdata[j].timetoken > tempdata[j + 1].timetoken) {
                  let temp = tempdata[j];
                  tempdata[j] = tempdata[j + 1];
                  tempdata[j + 1] = temp;
                }
              }
            }
            tempdata.forEach(ele => {
              if (ele.author === this.myChannel) {
                ele.role = "user"
              } else {
                ele.role = "guest"
              }
            });
            this.chatBox = tempdata;
            console.log("CBOS : ",this.chatBox)
          }
        }
      }
    )
  }

  getStoreQuery() {
    this.adminService.getStoreQuery(this.storeId).subscribe((res: any) => {
       console.log(res);
      this.FeedbackData = res.data.filter((x: any) => x.messagetype === "Feedback");
      this.RequestData = res.data.filter((x: any) => x.messagetype === "Request");
      this.suggesionData = res.data.filter((x: any) => x.messagetype === "Suggesion");
      console.log(this.FeedbackData);
      console.log(this.RequestData);
      console.log(this.suggesionData);
    })
  }

  sendMsg() {
    if (this.msgForm.invalid) {
      return
    }
    this.pubnub.setUUID(this.userChannel);
    let d1 = new Date();
    let d2 = d1.toLocaleTimeString();
    const msg = {
      time: d2,
      content: this.msgForm.get('msgField')?.value,
      id: Math.random().toString(16).substr(2),
      user:this.userInfo[0]
    }
    console.log(msg);
    this.pubnub.publish({
      channel: this.myChannel,
      message: msg,
      messageType: "Request"
    }, (status: any, response: any) => {
      console.log(status);
      console.log(response);
      this.msgForm.reset();
      this.fetChMessage();
    });
  }


  goBack() {
    this.location.back();
  }  

  ngOnDestroy(): void {
    this.pubnub.unsubscribeAll();
    this.storeId = "";
    this.userList = [];
  }
}
