import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class AuthGardSuperAdminService implements CanActivate {

  constructor(private router: Router) { }
  canActivate(): boolean {
    const local: any = localStorage.getItem('currentUser')
    const res = JSON.parse(local);
    const token = res.token;
    const role = res.user.role[0];

    if (token && role === "superAdmin") {
      //  alert("you have permition to access this content");

      return true;
    }
    else {
      //  alert("only super Admin has perition to access this contents");
      // this.router.navigate(['/home'])
      this.router.navigateByUrl('/home')
      return false;

    }
  }

}
