import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SuperAdminService } from 'src/app/Services/super-admin.service';
import { ValstoreRegisterService } from '../../../Services/valstore-register.service';

@Component({
  selector: 'app-info-sidebar',
  templateUrl: './info-sidebar.component.html',
  styleUrls: ['./info-sidebar.component.scss']
})
export class InfoSidebarComponent implements OnInit {

  storeId: any;
  currentUser: any;
  storeList: any = [];
  storeName: any;
  storeType: any;
  storeNames: any = [];
  username: any;
  byzcode: any;
  storeImage: any;
  storeDetails: any;
  constructor(private route: Router, private ValstoreRegisterServiceObj: ValstoreRegisterService, private superAadminServices: SuperAdminService) {
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const storeId = store.user.storeId;

    this.ValstoreRegisterServiceObj.getListOfStore().subscribe((response: any) => {

      console.log(response);
      this.storeList = response;
      this.storeList.forEach((element: any) => {
        this.storeNames.push(element.name);
      });
      const presentStore = this.storeList.filter((ele: any) => ele._id == storeId);
      console.log(presentStore[0].storeImagesId);

      // if (presentStore[0].storeImagesId.storeLogo != null) {
      //   this.storeImage = "https://s3.wasabisys.com/valart/" + presentStore[0].storeImagesId.storeLogo;
      // }
      if (presentStore[0].storeImagesId.storeLogo == null) {
        //    this.storeImage = "assets/images/user.jpg";
      }

    });
  }

  async ngOnInit() {
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    var token = store.token;
    this.username = store.user.ownerName;

    const userData: any = localStorage.getItem("storeDetails");
    const data = JSON.parse(userData);
    this.storeImage = "/assets/images/noImg.png"
    this.storeDetails = await this.ValstoreRegisterServiceObj.getStoreDetail().toPromise();
    this.storeImage = "https://s3.wasabisys.com/valart/t_" + this.storeDetails[0].storeImagesId.storeLogo


    this.getUserById();
  }


  getUserById() {
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const userId = store.user.id;
    this.superAadminServices.getUserById(userId).subscribe((res: any) => {

      this.byzcode = res.message[0].code;
      console.log(this.byzcode);

    })
  }
  setStoreId(storeName: any) {
    var storeType;

    this.storeList.forEach((element: any) => {
      if (element.name == storeName) {
        this.storeId = element._id;
        storeType = element.storeType;
        //this.data.setStoreName(storeName);
        this.storeName = element.name;
      }
    });
    this.currentUser = localStorage.getItem('currentUser');
    this.currentUser = JSON.parse(this.currentUser);
    this.currentUser.user.storeId = this.storeId;
    this.currentUser.user.storeName = this.storeName;
    localStorage.setItem("currentUser", JSON.stringify(this.currentUser));
    // this.data.setStoreId(this.storeId);
    this.storeType = localStorage.getItem('storeType');
    //this.storeType = JSON.parse(this.storeType);
    this.storeType = storeType;
    localStorage.setItem('storeType', this.storeType);
    this.route.navigate(['/admin/dashboard']);


  }
}
