import { Injectable } from '@angular/core';
// import { timeStamp } from 'console';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DataService {




  valstoreInfo: any;
  storeId: any;
  userId: any;
  storeName: any;
  shipment_id: any;
  review: any;
  prodReview: any;
  storeReview: any;
  storeDetails: any;
  constructor() { }
  Mainheader = new Subject();
  Mobileno: any;
  email: any;
  valStoreSetupForm: any;
  documentData: any;


  updateProduct: any;
  Order: any;
  storeType: any;
  fssai: any;
  image: any;
  imageUrl: any;
  editDetail: any;
  editDetailInfo: any;
  valstoreDesc: any;
  serviseStoreType = false;
  valstorename: any;
  checkFilename: any;
  adharImg: any = [];
  panPik: any = [];
  storeVideos:any=null;

  removePanPik(file: any) {
    this.panPik = this.panPik.filter((x: any) => x != file)
  }
  removeAdharPik(file: any) {
    this.adharImg = this.adharImg.filter((x: any) => x != file);

  }
  setPanPik(file: any) {
    // this.panPik = file;
    this.panPik.push(file)
  }
  getPanPik() {
    return this.panPik;
  }
  setAharImg(file: any) {
    // this.adharImg = file;
    this.adharImg.push(file);
  }
  getAharImg() {
    return this.adharImg;
  }
  setCheckName(checkFilename: any) {
    this.checkFilename = checkFilename;
  }
  getCheckName() {
    return this.checkFilename;
  }
  setValStoreName(valstorename: any) {
    this.valstorename = valstorename;
  }
  getValstoreName() {
    return this.valstorename;
  }
  setServiceType(value: boolean) {
    this.serviseStoreType = value;
  }
  getServiceType() {
    return this.serviseStoreType;
  }
  setMainheader(value: boolean) {
    this.Mainheader.next(value);
  }

  getMainheader() {
    return this.Mainheader;
  }

  setMobNo(mobno: any) {
    this.Mobileno = mobno;
  }

  getMobno() {
    return this.Mobileno;

  }

  setEmail(email: any) {
    this.email = email;
  }
  getEmail() {
    return this.email;
  }

  setupvalstore(form: any) {
    this.valStoreSetupForm = form;
  }

  getupvalstore() {
    return this.valStoreSetupForm;
  }

  setDocumentData(data: any) {
    this.documentData = data;
  }

  getDocumentData() {
    return this.documentData;
  }

  setValstroeInfo(info: any) {
    this.valstoreInfo = info;
  }

  getValstroeInfo() {
    return this.valstoreInfo;
  }

  setStoreId(id: any) {
    this.storeId = id;
  }

  getStoreId() {
    return this.storeId;
  }

  setUpdateProduct(product: any) {
    this.updateProduct = product;
  }

  getUpdateProduct() {
    return this.updateProduct;

  }

  getOrder() {
    return this.Order;
  }
  setOrder(order: any) {
    this.Order = order;
  }

  setStoreType(storeType: any) {
    this.storeType = storeType;
  }

  getStoreType() {
    return this.storeType;
  }

  setFssai(fssai: any) {
    this.fssai = fssai;
  }

  getFssai() {
    return this.fssai;
  }

  setImage(img: any) {

    this.image = img;
  }

  getImage() {

    return this.image;
  }


  setImageUrl(url: any) {
   
    this.imageUrl = url;
  }

  getImageUrl() {

    return this.imageUrl;
  }

  setEditDetail(detail: any) {

    this.editDetail = detail;

  }
  getEditDetail() {


    return this.editDetail;
  }

  setEditDetailInfo(detailInfo: any) {


    this.editDetailInfo = detailInfo;
  }
  getEditDetailInfo() {

    return this.editDetailInfo;
  }

  setValstoreDesc(desc: any) {
    this.valstoreDesc = desc;

  }

  getValstoreDesc() {
    return this.valstoreDesc;
  }

  setShiprocketId(id: any) {
    this.shipment_id = id;
  }

  getShiprocketId() {
    return this.shipment_id;
  }

  setStoreReview(review: any) {
    this.storeReview = review;
  }
  getStoreReview() {
    return this.storeReview;
  }

  setProductReview(review: any) {
    this.prodReview = review;
  }
  getProductReview() {
    return this.prodReview;
  }

  setReview(review: any) {
    this.review = review;
  }

  getReview() {
    return this.review
  }

  setStoreDetails(data: any) {
    this.storeDetails = data;

  }

  getStoreDetails() {
    return this.storeDetails;
  }

 
   setVideo(video:any)
  {
     this.storeVideos=video;
  }

  getVideo(){
    if(this.storeVideos==null){
      return null;
    }
    else{
  
       return this.storeVideos;
    }

  }

}

