import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-header2',
  templateUrl: './header2.component.html',
  styleUrls: ['./header2.component.scss']
})
export class Header2Component implements OnInit {

  storedData: any;
  role: any;
  storeId: any;
  superAdmin = false;
  storeAdmin = false;

  constructor(private router: Router) {
    this.storedData = localStorage.getItem('currentUser');
    this.storedData = JSON.parse(this.storedData);

  }

  ngOnInit(): void {
    this.role = this.storedData.user.role[0];
    if (this.storedData.user.role.includes("superAdmin")){
      this.superAdmin = true
    }
    if (this.storedData.user.role.includes("storeAdmin")){
      this.storeAdmin = true
    }

      //   console.log(this.role);
      this.storeId = localStorage.getItem("storeId")
    console.log(this.storeId);

  }

}
