import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { FooterComponent } from 'src/app/footer/footer.component'
import { CategoryService } from 'src/app/Services/category.service';
import { DataService } from 'src/app/Services/data.service';
import { ValstoreRegisterService } from 'src/app/Services/valstore-register.service';
import { environment } from 'src/environments/environment';
import { any } from 'underscore';

@Component({
  selector: 'app-valstore-info',
  templateUrl: './valstore-info.component.html',
  styleUrls: ['./valstore-info.component.scss']
})
export class ValstoreInfoComponent implements OnInit {
  listOfStoreNames: any = [];
  mainCategory: any = [];
  foodCategory: any = [];
  productCategory: any = [];
  servicesCategory: any = [];
  storeNameerr: boolean = false;
  storeNamedone: boolean = false;
  storeCategory: any;
  storeCategory2: any;
  alertMsg: any;
  formData: any = new FormData();
  valstoreForm2 = new FormGroup({

    valstorename: new FormControl(),
    valstoredesc: new FormControl()
  });

  valstoreForm = new FormGroup({
    storeType: new FormControl(),
    storecategory: new FormControl(),
    valstorename: new FormControl(),
    valstoredesc: new FormControl()
  });
  currentUser: any;
  storeType: any;
  constructor(
    private data: DataService, private http: HttpClient,
    private CategoryServiceObj: CategoryService,
    private ValstoreRegisterServiceObj: ValstoreRegisterService,
    private router: Router) {
    this.getMainCategories();

    this.ValstoreRegisterServiceObj.checkNameAvailability().subscribe((response: any) => {

      this.listOfStoreNames = response.data;
    })



  }

  ngOnInit(): void {

    this.valstoreForm = new FormGroup({
      storeType: new FormControl('', [Validators.required]),
      storecategory: new FormControl('', [Validators.required]),
      valstorename: new FormControl('', [Validators.required]),
      valstoredesc: new FormControl('', [Validators.required])

    })
    this.valstoreForm2 = new FormGroup({

      valstorename: new FormControl('', [Validators.required]),
      valstoredesc: new FormControl('', [Validators.required])

    })

  }

  checkavailability(storeName: any) {

    if (this.listOfStoreNames.includes(storeName)) {
      this.storeNameerr = true;
      this.storeNamedone = false;
    }
    else {
      this.storeNamedone = true;
      this.storeNameerr = false;
    }

    console.log("Inside the availability:" + this.valstoreForm.value.valstorename);
  }

  SaveValstoreInfo() {

    // if (this.valstoreForm.value.storeType == "Services") {
    //   this.data.setServiceType(true);

    //   this.valstoreForm.controls.valstorename.setValue(this.valstoreForm2.value.valstorename);
    //   this.valstoreForm.controls.valstoredesc.setValue(this.valstoreForm2.value.valstoredesc);
    // }
    // this.data.setValstroeInfo(this.valstoreForm);
    // this.data.setStoreType(this.valstoreForm.value.storeType);
    // this.data.setValstoreDesc(this.valstoreForm.value.valstoredesc);
    // this.data.setValStoreName(this.valstoreForm.value.valstorename)
    this.RegisterValStore();

  }
  RegisterValStore() {

    if (this.valstoreForm.value.storeType != "Services") {
      console.log("store type  : product or food");

      this.formData.append("storeType", this.valstoreForm.value.storeType);
      this.formData.append("name", this.valstoreForm.value.valstorename);
      this.formData.append("description", this.valstoreForm.value.valstoredesc);
    }

    if (this.valstoreForm.value.storeType == "Services") {
      console.log("store type  : is service");
      this.formData.append("storeType", this.valstoreForm.value.storeType);
      this.formData.append("name", this.valstoreForm2.value.valstorename);
      this.formData.append("description", this.valstoreForm2.value.valstoredesc);
    }
    this.formData.append("categoryId", this.valstoreForm.value.storecategory);

    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    var token = store.token;
    console.log(store.user.ownerName);

    this.formData.append("contactPerson", store.user.ownerName);
    const headers = new HttpHeaders({ 'Authorization': token });
 
    return this.http.post(`${environment.BASE_URL}/store/create`, this.formData, { headers: headers }).subscribe((res: any) => {
      console.log(res);
  
      if (this.valstoreForm.value.storeType == "Services") {
        const data = {
          storeName: this.valstoreForm2.value.valstorename,
          logo: ""
        }
        localStorage.setItem("storeDetails", JSON.stringify(data));
      }
      if (this.valstoreForm.value.storeType != "Services") {
        const data = {
          storeName: this.valstoreForm.value.valstorename,
          logo: ""
        }
        localStorage.setItem("storeDetails", JSON.stringify(data));

      }
      this.currentUser = localStorage.getItem('currentUser');
      this.currentUser = JSON.parse(this.currentUser);
      this.currentUser.user.storeId = res.data._id;
      this.currentUser.user.storeName = res.data.name;
      localStorage.setItem("currentUser", JSON.stringify(this.currentUser));

      this.storeType = localStorage.getItem('storeType');

      this.storeType = res.data.storeType;
      localStorage.setItem('storeType', this.storeType);

      // alert("store Created successfully");
      this.router.navigateByUrl('/valstoredoc');

    }, (error) => {
      console.log(error);

    })
  }

  getMainCategories() {

    this.CategoryServiceObj.getAllCategory().subscribe((response: any) => {

      this.mainCategory = response;
      console.log("this.mainCategory:");
      console.log(this.mainCategory);


      this.mainCategory.forEach((element: any) => {

        if (element.storeType == "Food") {
          var Category: any = [];
          Category = element;
          this.foodCategory.push(Category);
        }
        else if (element.storeType == "Product") {
          var Category: any = [];
          Category = element;
          this.productCategory.push(Category);
        }
        else if (element.storeType == "Services") {
          var Category: any = [];
          Category = element;
          this.servicesCategory.push(Category);
          // console.log(this.servicesCategory);

        }


      });
    })



  }
}
