import { ChangeDetectionStrategy, ChangeDetectorRef, Component, EventEmitter, Input, OnInit, Output, SimpleChange } from '@angular/core';
import { DataService } from '../../../Services/data.service';


interface editData {
  detail: any;
  detailInfo: any;
}

@Component({
  selector: 'app-edit-info',
  templateUrl: './edit-info.component.html',
  styleUrls: ['./edit-info.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class EditInfoComponent implements OnInit {
  @Input() detail: any;
  //  @Input() detailInfo:any;
  incomingdetail: editData = { detail: '', detailInfo: '' };
  details: any;
  detailsInfo: any;
  @Output() onClick = new EventEmitter();
  @Output() onDone = new EventEmitter();
  detailInfo: any;

  constructor(private changeDetectorRef: ChangeDetectorRef, private data: DataService) {

  }

  ngOnInit(): void {

  }

  ngOnChanges(change: SimpleChange) {

    console.log(change);
    this.incomingdetail = this.detail;
    if (this.incomingdetail != undefined || null) {
      this.details = this.incomingdetail.detail;
      this.detailsInfo = this.incomingdetail.detailInfo;
    }



  }
  keyUp(event: any) {
    const seperator = '^[a-zA-Z ]*$';
    const maskSeperator = new RegExp(seperator, 'g');
    let result = maskSeperator.test(event.key); return result;
  }

  done(data: any) {

    this.data.setEditDetailInfo(data);

    this.onDone.emit();

  }

  // sendData(){
  // 
  //   return this.detailInfo;
  // }

  setdata() {

    this.onClick.emit();
    this.changeDetectorRef.detectChanges();
    setTimeout(() => {

    }, 200);

  }

}

