import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/Services/data.service';
import { ValstoreRegisterService } from 'src/app/Services/valstore-register.service';



@Component({
  selector: 'app-rejuvinate',
  templateUrl: './rejuvinate.component.html',
  styleUrls: ['./rejuvinate.component.scss']
})
export class RejuvinateComponent implements OnInit {
  storeDetails: any;
  storeName: any;
  storeLogo: any;
  imageURL: string = '';


  constructor(private ValstoreRegisterServiceObj: ValstoreRegisterService, private data: DataService) { }

  async ngOnInit() {
    var s1: any = localStorage.getItem("storeDetails");
    const s2 = JSON.parse(s1);
    this.storeName = s2.storeName;
    this.imageURL = "/assets/images/noImg.png"
    this.storeDetails = await this.ValstoreRegisterServiceObj.getStoreDetail().toPromise();
    const storeLogo = this.storeDetails[0].storeImagesId.storeLogo;
    console.log(storeLogo);
    console.log(s2.logo);
    // // 
    if (this.storeDetails[0].storeImagesId.storeLogo) {
      this.imageURL = "https://s3.wasabisys.com/homebyz/t_" + this.storeDetails[0].storeImagesId.storeLogo
    }
    console.log(this.imageURL);



    // this.storeDetails = await this.ValstoreRegisterServiceObj.getStoreDetail().toPromise();

    // this.storeName = this.storeDetails[0].name;
    // this.storeLogo = this.storeDetails[0].storeImagesId.storeLogo;
    // this.imageURL = "https://s3.wasabisys.com/valart/".concat(this.storeLogo);
    //     console.log(this.storeDetails);

  }
}


