import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataService } from '../Services/data.service';
import { RegisterService } from '../Services/register.service';
import { NgxSpinnerService } from "ngx-spinner";
import { ValstoreRegisterService } from '../Services/valstore-register.service';
import { SuperAdminService } from '../Services/super-admin.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-otpvalidation',
  templateUrl: './otpvalidation.component.html',
  styleUrls: ['./otpvalidation.component.scss']
})
export class OtpvalidationComponent implements OnInit {
  StoreDetails: any;
  email1: any;
  mobno1: any;
  otperr: boolean=false;
  constructor(private data: DataService, private http: HttpClient, private superAdminService: SuperAdminService, private RegisterServiceobj: RegisterService, private SpinnerService: NgxSpinnerService, private router: Router) { }
  mobno = "";
  email = "";
  eotp: any;
  motp: any;
  alertMsg: any;
  validOtp: boolean = false;

  public settings = {
    length: 4,
    numbersOnly: true,
    timer: 30
  }
  mobotp: boolean = false;
  emailotp: boolean = false;

  async ngOnInit(): Promise<void> {
    var currentUser: any = localStorage.getItem('currentUser');
    const c1 = JSON.parse(currentUser);
    
    const userId = c1.user._id;
    console.log("userid" + userId);
    
    this.superAdminService.getUserById(userId).subscribe((res: any) => {
   //   console.log(res);
     
      this.email = res.message[0].email;
      this.mobno = res.message[0].mobile;
      this.mobno1 = this.mobno.replace(/\d(?=\d{4})/g, "*");
      console.log(this.mobno1);
      this.email1 = this.email.replace(/\D(?=\D{12})/g, "*");
      console.log(this.email1);
    })
   
  }

  handleError() {
    this.alertMsg = null;
  }

  onInputChange(e: any) {
    console.log(e);
    if (e.length == this.settings.length) {
      // e will emit values entered as otp and,
      console.log('otp is', e);
      this.mobotp = true;
      this.motp = e;
      // this.validOtp = true;
      if (this.motp.length == 4 && this.eotp.length == 4) {
        this.validOtp = true;
      }
    } else if (e == -1) {
      // if e == -1, timer has stopped
      console.log(e, 'resend button enables');
    } else if (e == -2) {
      // e == -2, button click handle
      console.log('resend otp m');
      this.resendOtpForMobile();
    }

  }

  onInputChange2(otp: any) {

    if (otp.length == this.settings.length) {
      // e will emit values entered as otp and,
      console.log('otp is', otp);
      this.emailotp = true;
      this.eotp = otp;
      if (this.motp.length == 4 && this.eotp.length == 4) {
        this.validOtp = true;
      }
      // this.validOtp=true;


    } else if (otp == -1) {
      this.validOtp = false;
      // if e == -1, timer has stopped
      console.log(otp, 'resend button enables');
    } else if (otp == -2) {
      this.validOtp = false;
      // e == -2, button click handle
      console.log('resend otp e');
      this.resendOtpForEmail();
    }

  }

  resendOtpForMobile() {
    var token: any = localStorage.getItem('currentUser');
    token = JSON.parse(token);
    console.log(token.token);
    
    const body = {
      mobile: this.mobno

    }
    //var  mobile= this.mobno;

    const headers = new HttpHeaders({ 'Authorization': token.token });
    this.http.post(environment.BASE_URL + "/resend-otp/mobile", body, { headers: headers }).subscribe((res: any) => {
      
      console.log(res);

    })
  }

  resendOtpForEmail() {
    var token: any = localStorage.getItem('currentUser');
    token = JSON.parse(token);
    console.log(token.token);
    
    const body = {
      email: this.email

    }

    const headers = new HttpHeaders({ 'Authorization': token.token });
    this.http.post(environment.BASE_URL + "/resend-otp/email", body, { headers: headers }).subscribe((res: any) => {
      
      console.log(res);

    })
  }


  validateOtp() {
    var token: any = localStorage.getItem('currentUser');
    token = JSON.parse(token);
    const otpMobile = this.motp;
    console.log(token.token);
    this.SpinnerService.show();
    this.RegisterServiceobj.accountActivation(token.token, this.eotp, otpMobile).subscribe(
      (response: any) => {
        

        console.log(response);
        console.log("validation response:" + response.message);
        console.log("validation response success:" + response.success);

        if (response.success) {
          this.SpinnerService.hide();
          //this.alertMsg = "Your request has been submitted, our team will get back to you soon";
          alert("Your request has been submitted, our team will get back to you soon")
          localStorage.removeItem('currentUser');
          this.router.navigate(['/home']);
          // this.alertMsg = "Your request has been submitted, our team will get back to you soon";

        }
        else{
          this.SpinnerService.hide();
         this.otperr=true;
        }
      },
      (error: any) => {

        this.SpinnerService.hide();
        //   this.alertMsg = error.error.error;
        alert(error.error.error)
        this.router.navigate(['/register']);
      });
  }


}
