import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { AdminService } from './admin.service';

@Injectable({
  providedIn: 'root'
})
export class AdminAuthGardService implements CanActivate {

  constructor(private router: Router, private adminService: AdminService) { }
  canActivate(): boolean {
    const status: any = localStorage.getItem("storeStatus")
    const storeStatus = JSON.parse(status);
    if (storeStatus == true) {
      //  alert("you have permition to access this content");

      return true;
    }
    else {
      // alert("you dont have permition to access this content.");
      this.router.navigate(['/admin/account'])
      return false;

    }
  }

}
