import { AgmCoreModule } from '@agm/core';
import { DatePipe } from '@angular/common';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatTabsModule } from '@angular/material/tabs';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule, Routes } from '@angular/router';
import { NgCalendarModule } from 'angular-customizable-calendar';
import { AngularOtpLibModule } from 'angular-otp-box';
import { NgWizardConfig, NgWizardModule, THEME } from 'ng-wizard';
import { ImageCropperModule } from 'ngx-image-cropper';
import { NotificationsComponent } from './admin/components/notifications/notifications.component';
import { AddVideoStreamComponent } from './admin/components/video-stream/add-video-stream/add-video-stream.component';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { BaseComponent } from './base/base.component';
import { CarouselComponent } from './carousel/carousel.component';
import { CartComponent } from './cart/cart.component';
import { DeleteMenuComponent } from './delete-menu/delete-menu.component';
import { EditPanComponent } from './edit-pan/edit-pan.component';
import { FaqComponent } from './faq/faq.component';
import { FooterComponent } from './footer/footer.component';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';
import { HeaderComponent } from './header/header.component';
import { HomeComponent } from './home/home.component';
import { HowItWorksComponent } from './how-it-works/how-it-works.component';
import { ImageCropperComponent } from './image-cropper/image-cropper.component';
import { LiveStreamComponent } from './live-stream/live-stream.component';
import { LoginComponent } from './login/login.component';
import { LogoutComponent } from './logout/logout.component';
import { MainHomeComponent } from './main-home/main-home.component';
import { MyVideosComponent } from './my-videos/my-videos.component';
import { OffersComponent } from './offers/offers.component';
import { OrderedVideosComponent } from './ordered-videos/ordered-videos.component';
import { OtpvalidationComponent } from './otpvalidation/otpvalidation.component';
import { RecVideoComponent } from './rec-video/rec-video.component';
import { RegisterComponent } from './register/register.component';
import { ScheduleListComponent } from './schedule-list/schedule-list.component';
import { ScheduleComponent } from './schedule/schedule.component';
import { SellingOnValartComponent } from './selling-on-valart/selling-on-valart.component';
import { ActivatedGardService } from './Services/activated-gard.service';
import { AdminAuthGardService } from './Services/admin-auth-gard.service';
import { AuthGardSuperAdminService } from './Services/auth-gard-super-admin.service';
import { RegisterService } from './Services/register.service';
import { SharedModule } from './shared/shared/shared.module';
import { StoreDetailsComponent } from './store-details/store-details.component';
import { StoreHeaderComponent } from './store-header/store-header.component';
import { StoreItemDetailsComponent } from './store-item-details/store-item-details.component';
import { AddAddressComponent } from './store-register/add-address/add-address.component';
import { SetupValstoreComponent } from './store-register/setup-valstore/setup-valstore.component';
import { ValstoreDocumentComponent } from './store-register/valstore-document/valstore-document.component';
import { ValstoreInfoComponent } from './store-register/valstore-info/valstore-info.component';
import { StoreReviewComponent } from './store-review/store-review.component';
import { SubHomeComponent } from './sub-home/sub-home.component';
import { SupportComponent } from './support/support.component';
import { LiveVideoOrderComponent } from './live-video-order/live-video-order.component';
import { AlertDialogComponent } from './alert-dialog/alert-dialog.component';


const appRoutes: Routes = [
  {
    path: '',
    redirectTo: '/home',
    pathMatch: 'full'
  },
  {
    path: 'admin',
    loadChildren: () => import('./admin/admin.module').then(m => m.AdminModule),
    canActivate: [ActivatedGardService]
  },
  {
    path: 'superAdmin',
    loadChildren: () => import('./super-admin/super-admin.module').then(m => m.SuperAdminModule),
    canActivate: [AuthGardSuperAdminService]
  },
  { path: 'howitworks', component: HowItWorksComponent },
  { path: 'sellnvalart', component: SellingOnValartComponent },
  { path: 'support', component: SupportComponent },
  { path: 'faq', component: FaqComponent },
  { path: 'register', component: RegisterComponent },
  { path: 'otpvalidation', component: OtpvalidationComponent },
  { path: 'login', component: LoginComponent },
  { path: 'regvalstore', component: ValstoreInfoComponent },
  { path: 'valstoredoc', component: ValstoreDocumentComponent },
  { path: 'setupvalstore', component: SetupValstoreComponent },
  { path: 'setupvalstore/:id', component: SetupValstoreComponent },
  { path: 'offers', component: OffersComponent },
  { path: 'schedule-list', component: ScheduleListComponent },
  { path: 'store/:id/:key', component: StoreDetailsComponent },
  { path: 'item-details', component: StoreItemDetailsComponent },
  { path: 'stream', component: LiveStreamComponent },
  { path: 'video', component: RecVideoComponent },
  { path: 'cart', component: CartComponent },
  { path: 'pandetails', component: EditPanComponent },
  { path: 'my-videos', component: MyVideosComponent },
  { path: 'video-orders', component: OrderedVideosComponent },
  { path: 'store-review', component: StoreReviewComponent },
  { path: 'add-address', component: AddAddressComponent },
  { path: 'notifications', component: NotificationsComponent },
  {path:'live-video-order/:id',component:LiveVideoOrderComponent},
  { path: 'home', component: HomeComponent },
  {
    path: '**',
    redirectTo: '/home',
    pathMatch: 'full'
  }
]

const ngWizardConfig: NgWizardConfig = {
  theme: THEME.circles
};



@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    HowItWorksComponent,
    SellingOnValartComponent,
    FaqComponent,
    SupportComponent,
    LoginComponent,
    RegisterComponent,
    LogoutComponent,
    FooterComponent,
    ValstoreInfoComponent,
    ValstoreDocumentComponent,
    SetupValstoreComponent,
    OffersComponent,
    DeleteMenuComponent,
    CarouselComponent,
    HomeComponent,
    OtpvalidationComponent,
    MainHomeComponent,
    SubHomeComponent,
    ImageCropperComponent,
    ScheduleComponent,
    ScheduleListComponent,
    StoreDetailsComponent,
    StoreItemDetailsComponent,
    LiveStreamComponent,
    RecVideoComponent,
    ForgotPasswordComponent,
    BaseComponent,
    CartComponent,
    EditPanComponent,
    StoreHeaderComponent,
    MyVideosComponent,
    OrderedVideosComponent,
    StoreReviewComponent,
    AddAddressComponent,
    LiveVideoOrderComponent,
    AlertDialogComponent,
  ],
  imports: [

    // MatButtonToggleModule,
    // PdfViewerModule,
    // MatTableModule,
    // MatDialogModule,
    // MatTooltipModule,
    // TooltipModule,
    // MatStepperModule,
    // MatButtonModule,
    // MatTabsModule,
    // MatSnackBarModule,
    SharedModule,
    FormsModule,
    ReactiveFormsModule,
    BrowserAnimationsModule,
    BrowserModule,
    HttpClientModule,
    AppRoutingModule,
    AngularOtpLibModule,
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyA1-puWviMUJNN-2TFcJ7Trpxyg6oRz7ZY',
      libraries: ['places']
    }),
    RouterModule.forRoot(
      appRoutes
    ),

    ImageCropperModule,
    NgWizardModule.forRoot(ngWizardConfig),
    NgCalendarModule,
    SharedModule
  ],
  exports: [
    MatButtonToggleModule,
    NgWizardModule,
    MatTabsModule
  ],
  providers: [LoginComponent,
    DatePipe,
    HttpClient,
    RegisterService,
    ImageCropperComponent,
    AuthGardSuperAdminService,
    ActivatedGardService,
    AdminAuthGardService,
    AddVideoStreamComponent,
    ScheduleComponent 
  ],

  bootstrap: [AppComponent]
})
export class AppModule { }
